import { PlanningCollapsablePanelComponent } from './../planning-collapsable-panel/planning-collapsable-panel.component';
import { AgGridHelper } from '../../../../common/core/grid/helpers/ag-grid.helper';
import { PlanningRequirementsQuery } from './../../../../common/store/planning-requirements/planning-requirements.query';
import {
  RequirementRowHeaderComponent, RequirementRowHeaderParams,
} from './components/requirement-row-header/requirement-row-header.component';
import { RequirementCellComponent, RequirementCellParams } from './components/requirement-cell/requirement-cell.component';
import { filter, tap } from 'rxjs/operators';
import { TeamPlanningsQuery } from '../../../../common/store/team-plannings/team-plannings.query';
import { TeamPlanning } from '../../../../common/models/planning/team-planning.model';
import { DateHelperService } from '../../../../common/helpers/DateHelper.service';
import { Component, Input, OnInit, OnDestroy, OnChanges, SimpleChanges, ViewChild, ElementRef, Output, EventEmitter } from '@angular/core';
import { ColGroupDef, GridOptions, GridApi, GridReadyEvent, ColDef } from 'ag-grid-community';
import { Observable, Subscription } from 'rxjs';
import { PlanningDimensions } from '../../planning-generator.page';
import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';
import { TranslocoService } from '@ngneat/transloco';
import { BlankColumnComponent } from 'src/app/common/core/grid/components/blank-column/blank-column.component';
import { PlanningPanelAction } from '../planning-collapsable-panel/planning-collapsable-panel.component';
import { MatDialog } from '@angular/material/dialog';
import {
    RequirementEditionDialogComponent,
    RequirementEditionDialogData,
} from './components/requirement-edition-dialog/requirement-edition-dialog.component';

export type RequirementGridComponentExpansion = 'normal' | 'full';

@Component({
  selector: 'epop-requirement-grid',
  templateUrl: './requirement-grid.component.html',
  styleUrls: ['./requirement-grid.component.scss']
})
export class RequirementGridComponent implements OnInit, OnDestroy, OnChanges {

  @Input()
  public planningGridOptions: GridOptions;
  @Output()
  public requirementPlanningExpansion: EventEmitter<RequirementGridComponentExpansion> = new EventEmitter();
  @Input()
  public unitId: number;

  public currentExpansion: RequirementGridComponentExpansion = 'normal';
  private gridApi: GridApi;

  public panelActions: Array<PlanningPanelAction> = [
    {
      getIcon: () => this.currentExpansion === 'normal' ? 'mdi/arrow-expand-vertical' : 'mdi/format-vertical-align-center',
      onClick: () => this.toggleExpandPanel()
    }
  ];

  public rightActions: Array<PlanningPanelAction> = [
    {
      getIcon: () => 'mdi/playlist-plus',
      onClick: () => this.openEditionDialog(),
    }
  ];


  @ViewChild('requirementPanel')
  private requirementPanel: PlanningCollapsablePanelComponent;

  /**
   * Width of side blank width to keep the same alignement with planning grid.
   */
  @Input()
  public planningAlignementSpace: number;

  public requirements: Array<PlanningRequirement>;

  private subscriptions: Array<Subscription> = [];

  public frameworkComponents = {
    RequirementCellComponent,
    RequirementRowHeaderComponent,
    BlankColumnComponent
  };

  public columnDefs: Array<ColGroupDef> = [];

  public gridOptions = {
    alignedGrids: [],
    groupHeaderHeight: 0,
    headerHeight: 0,
    onGridReady: this.onGridReady.bind(this)
  };

  private blankAlignementColumnDef: ColGroupDef = {
    groupId: 'blankAlignement',
    marryChildren: true,
    children: [
      {
        colId: 'blankAlignementColumn',
        pinned: 'right',
        width: 0, // default width. Will be updated after init.
        headerComponent: 'BlankColumnComponent',
      }
    ],
  };

  public planning: Observable<TeamPlanning> = this.teamPlanningsQuery.selectActive() as Observable<TeamPlanning>;

  constructor(
    private teamPlanningsQuery: TeamPlanningsQuery,
    private dialog: MatDialog,
    private planningRequirementsQuery: PlanningRequirementsQuery,
    private translocoService: TranslocoService) { }

  ngOnInit(): void {
    this.subscriptions.push(
      this.planning.pipe(
        filter(planning => planning !== undefined),
        tap(planning => this.generateColDefinition(planning))
      ).subscribe()
    );
    this.subscriptions.push(this.planningRequirementsQuery.selectAll().pipe(
      tap(requirements => this.updateRequirements(requirements))
    ).subscribe());
  }

  private onGridReady(gridReadyEvent: GridReadyEvent) {
    this.gridApi = gridReadyEvent.api;
    this.gridApi.setRowData(this.requirements);
  }

  private updateRequirements(newRequirements: PlanningRequirement[]) {
    this.requirements = newRequirements;
    this.gridApi?.setRowData(this.requirements);
  }

  ngOnChanges(changes: SimpleChanges): void {
    // When input planningAlignementSpace change, update the blank column size
    if(changes.planningAlignementSpace?.previousValue !== this.planningAlignementSpace) {
      this.updateBlankColumnSize();
    }
  }

  generateColDefinition(planning: TeamPlanning) {
    // Requirement name grid :
    this.columnDefs[0] = {
      marryChildren: true,
      children: [
        {
          width: PlanningDimensions.resourceColumnWidth,
          pinned: 'left',
          headerName: this.translocoService.translate('planningGenerator.planning.affectations.model.quotaConstrainst'),
          cellClass: 'epop-planning-grid-cell',
          cellRenderer: 'RequirementRowHeaderComponent',
          cellRendererParams: {
            unitId: this.unitId
          } as RequirementRowHeaderParams
        }
      ]
    };

    // Requirement cell
    this.columnDefs[1] = {
      marryChildren: true,
      children: this.getRequirementDates(planning).map(date => ({
        width: PlanningDimensions.planningColumnWidth.normal,
        minWidth: PlanningDimensions.planningColumnMinWidth - 1,
        headerName: '',
        field: 'planning.' + PlanningRequirement.getPlanningKey(date),
        cellRenderer: 'RequirementCellComponent',
        cellClass: 'epop-planning-grid-cell',
        cellRendererParams: {
          requirementDate: date
        } as RequirementCellParams
      }))
    };
    // Blank cell for alignement
    this.columnDefs[2] = this.blankAlignementColumnDef;
  }

  updateBlankColumnSize() {
    // Get column to update (alignement column)
    const blankAlignementColumn: ColDef = this.blankAlignementColumnDef.children[0];
    // Update column width
    blankAlignementColumn.width = this.getPlanningAlignementSpace();
    // Update grid :
    this.gridApi?.setColumnDefs(this.columnDefs);
  }

  public getGridOptions() {
    return this.gridOptions;
  }

  private getPlanningAlignementSpace(): number {
    let spaceWidth = this.planningAlignementSpace;
    if(this.gridApi) {
      // Check for scroll bar width :
      spaceWidth -= AgGridHelper.getVerticalScrollWitdh(this.gridApi);
    }
    return spaceWidth;
  }

  private getRequirementDates(planning): Array<Date> {
    return DateHelperService.getAllDays(planning.getStartDate(), planning.getEndDate());
  }

  public ngOnDestroy() {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  public toggleExpandPanel() {
    if(this.currentExpansion === 'normal') {
      this.requirementPanel.expandPanel();
      this.currentExpansion = 'full';
    } else {
      this.currentExpansion = 'normal';
    }
    this.requirementPlanningExpansion.next(this.currentExpansion);
  }

  private openEditionDialog(): void {
    // Open the dialog :
    this.dialog.open(RequirementEditionDialogComponent, {
      // To prevent close of dialog on "ESC" touch/outside clicks during input edition:
      disableClose: true,
        data: {
          planningRequirement: PlanningRequirement.generateEmptyPlanningRequirement(this.teamPlanningsQuery.getActive() as TeamPlanning),
          unitId: this.unitId
        } as RequirementEditionDialogData
    });
  }

}
