import { CheckboxCellParams } from './../../../../../../../../common/core/grid/components/checkbox-cell/checkbox-cell.component';
import { CheckboxCellComponent } from './../../../../../../../../common/core/grid/components/checkbox-cell/checkbox-cell.component';
import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
    ColDef,
    GridOptions,
    GridApi,
    GridReadyEvent,
    ValueFormatterParams,
    ICellRendererParams,
    RangeSelectionChangedEvent,
    RowNode,
    CellRangeParams,
    CellPosition,
    Column,
} from 'ag-grid-community';
import { format, getWeek } from 'date-fns';
import { NumericCellEditorComponent } from 'src/app/common/core/grid/components/numeric-cell-editor/numeric-cell-editor.component';
import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';
import {
  MatrixSelectionMode,
    RequirementMatrixSelectionHeaderComponent,
    RequirementMatrixSelectionHeaderParams,
} from './components/requirement-matrix-selection-header.component/requirement-matrix-selection-header.component';

type CellNode = {
  rowNode: RowNode; column: Column;
};

const cellSize = 20;
type RowModel = {
  day1: { date: Date; value: number };
  day2: { date: Date; value: number };
  day3: { date: Date; value: number };
  day4: { date: Date; value: number };
  day5: { date: Date; value: number };
  day6: { date: Date; value: number };
  day7: { date: Date; value: number };
  startDate: Date;
  endDate: Date;
  weekNumber: number;
};

const CHECKBOX_CELL_ID = 'checkboxCellId';

export type RequirementPlanningChangeEvent = {
  date: Date;
  newValue: number;
};

@Component({
  selector: 'epop-requirement-matrix-edition',
  templateUrl: './requirement-matrix-edition.component.html',
  styleUrls: ['./requirement-matrix-edition.component.scss']
})
export class RequirementMatrixEditionComponent implements OnInit {

  public groupValues = {
    day1: 0,
    day2: 0,
    day3: 0,
    day4: 0,
    day5: 0,
    day6: 0,
    day7: 0,
  };

  @Input()
  teamPlanning: TeamPlanning;

  @Input()
  planningRequirement: PlanningRequirement;

  @Output()
  public requirementPlanningChange: EventEmitter<RequirementPlanningChangeEvent> = new EventEmitter();

  private gridApi: GridApi;

  public columnDefs: Array<ColDef>;
  public gridOptions: GridOptions = {
    onGridReady: this.onGridReady.bind(this),
    alignedGrids: [],
    groupHeaderHeight: 0,
    animateRows: true,
    suppressContextMenu: true,
    defaultColDef: {
      suppressMenu: true
    },
    onRangeSelectionChanged: this.onGridSelectionChanged.bind(this),
    rowMultiSelectWithClick: true,
    enableRangeSelection: true,
    stopEditingWhenCellsLoseFocus: true
  };
  public frameworkComponents = {
    numericCellEditorComponent: NumericCellEditorComponent,
    checkboxCellComponent: CheckboxCellComponent,
    requirementMatrixSelectionHeaderComponent: RequirementMatrixSelectionHeaderComponent
  };

  public gridData: Array<Partial<RowModel>> = [];
  public selectedColumns: string[] = [];
  public selectionCheckboxHeaderComponent: RequirementMatrixSelectionHeaderComponent;

  constructor(private dateHelperService: DateHelperService) {}

  ngOnInit(): void {
    this.columnDefs = this.getPlanningGridDefinition();
    this.gridData = [];
    let rowData: Partial<RowModel>;
    this.teamPlanning.getPlanningDays().forEach(day => {
      if(!rowData) {
        // Init the row data :
        rowData = {
          startDate: day,
          weekNumber: this.getWeekNumber(day)
        };
      }
      const dayNumber = format(day, 'i');
      rowData['day' + dayNumber] = {
        date: day,
        value: this.planningRequirement.getRequirement(day)?.getMinValue() ?? 0,
      };
      // If sunday, add the row to data :
      if(dayNumber === '7') {
        rowData.endDate = day;
        this.gridData.push(rowData);
        rowData = undefined;
      }
    });

  }

  private onGridReady(event: GridReadyEvent): void {
    this.gridApi = event.api;
  }

  private onGridSelectionChanged(_event: RangeSelectionChangedEvent) {
    // Update checkbox states :
    this.updateCheckboxStates();
    // Update groupValues view :
    this.updateGroupValuesView();
    // Remove focus to prevent visual glitch after selection :
    this.gridApi.clearFocusedCell();
  }

  private updateCheckboxStates(): void {
    const fullRowIndexes = this.getFullSelectedRowIndexes();
    this.gridApi.forEachNode(node => {
      const cellComponent = this.gridApi.getCellRendererInstances({
        rowNodes: [node],
        columns: [CHECKBOX_CELL_ID]
      })[0] as any;
      const checkboxCellComponent = (cellComponent.getFrameworkComponentInstance() as CheckboxCellComponent);
      if(fullRowIndexes.indexOf(node) > -1) {
        checkboxCellComponent.setCheckboxState(true);
      } else {
        checkboxCellComponent.setCheckboxState(false);
      }
    });
  }

  private updateGroupValuesView(): void {
    const nodes = this.getAllNodeFromRanges();
    // Only show and compute groupCell edition with more than 2 elements selected :
    if(nodes.length > 1) {
      const columnCheck = {};
      nodes.forEach(element => {
        const colId = element.column.getColId();
        columnCheck[colId] = true;
      });
      this.selectedColumns = Object.keys(columnCheck);
    } else {
      // Disable groupCell edition :
      this.selectedColumns = [];
    }
  }

  private getFullSelectedRowIndexes(): Array<RowNode> {
    const fullRowIndexes: Array<RowNode> = [];
    const rowNodesReferences: Array<RowNode> = [];
    // Check completed rows and update checkboxes :
    const nodes = this.getAllNodeFromRanges();
    const allNodeCounts = [];
    nodes.forEach(element => {
      const rowIndex = element.rowNode.rowIndex;
      if(allNodeCounts[rowIndex] === undefined) {
        allNodeCounts[rowIndex] = {};
        rowNodesReferences[rowIndex] = element.rowNode;
      }
      const colId = element.column.getColId();
      if(colId.startsWith('day')) {
        allNodeCounts[rowIndex][colId] = 1;
      }
    });
    allNodeCounts.forEach((rowNodeCount, index) => {
      if(Object.keys(rowNodeCount).length === 7) {
        // This is a full row hehe
        fullRowIndexes.push(rowNodesReferences[index]);
      }
    });
    return fullRowIndexes;
  }

  private getPlanningGridDefinition(): Array<ColDef> {
    return [
      this.generateRowSelectorColumn(),
      ...this.generateDaysColumns(),
      ...this.generateStaticColumns()
    ];
  }

  private generateRowSelectorColumn(): ColDef {
    return {
      width: cellSize,
      colId: CHECKBOX_CELL_ID,
      headerName: '',
      cellClass: 'cell-matrix',
      cellRenderer: 'checkboxCellComponent',
      headerComponent: 'requirementMatrixSelectionHeaderComponent',
      headerComponentParams: {
        onSelect: (mode) => this.onSelectionModeChange(mode),
        onReady: component => this.selectionCheckboxHeaderComponent = component
      } as RequirementMatrixSelectionHeaderParams,
      cellRendererParams: {
        onCheckboxChange: (isChecked, cellParams) => this.onCheckboxRowClicked(isChecked, cellParams),
        stopClickEventPropagation: true
      } as CheckboxCellParams
    };
  }

  private onCheckboxRowClicked(isChecked: boolean, cellParams: ICellRendererParams): void {
    this.selectCompleteRow(isChecked, cellParams.node);
    // Upate checkbox header state :
    if(this.selectionCheckboxHeaderComponent !== undefined) {
      this.selectionCheckboxHeaderComponent.resetCheckboxState();
    }
  }

  private generateDaysColumns(): Array<ColDef> {
    const columns: Array<ColDef> = [];
    const weekDaysLabel = this.dateHelperService.getWeekDaysLabel();
    for (let i = 1; i <= 7; i++) {
      const colField = `day${i}`;
      const colDef: ColDef = {
        colId: colField,
        headerClass: 'header-matrix',
        width: cellSize,
        cellClass: 'cell-matrix',
        enableCellChangeFlash: true,
        editable: true,
        cellEditor: 'numericCellEditorComponent',
        onCellValueChanged: (params) => this.onCellEdited(params.data, params.column.colId, params.newValue),
        field: colField,
        valueGetter: params => params.data[colField].value,
        valueSetter: params => {
          this.updateDataValue(params.data, colField, params.newValue);
          return true;
        },
        headerName: weekDaysLabel[i-1]
      };
      columns.push(colDef);
    }
    return columns;
  }

  private getWeekNumber(date: Date): number {
    return getWeek(date,
      {
        weekStartsOn: 1
      });
  }

  private updateDataValue(data: RowModel, modelField, value) {
    data[modelField].value = value;
    return data;
  }

  private selectCompleteRow(setSelected: boolean, node: RowNode): void {
    if(setSelected) {
      this.gridApi.addCellRange({
        rowStartIndex: node.rowIndex,
        rowEndIndex: node.rowIndex,
        columnStart: 'day1',
        columnEnd: 'day7',
      });
    } else {
      // Unselect all node of this row
      this.deselectRowFromRanges(node.rowIndex);
    }
  }

  private selectAllRows(): void {
    this.gridApi.clearRangeSelection();
    this.gridApi.forEachNode(node => {
      this.selectCompleteRow(true, node);
    });
  }

  private selectImpairWeekRows(): void {
    this.gridApi.clearRangeSelection();
    this.gridApi.forEachNode(node => {
      const weekNumber = this.getWeekNumber(node.data.day1.date);
      if (weekNumber%2 ===1) {
        this.selectCompleteRow(true, node);
      }
    });
  }

  private selectPairWeekRows(): void {
    this.gridApi.clearRangeSelection();
    this.gridApi.forEachNode(node => {
      const weekNumber = this.getWeekNumber(node.data.day1.date);
      if (weekNumber%2 === 0) {
        this.selectCompleteRow(true, node);
      }
    });
  }

  private deselectRowFromRanges(rowToDeselect: number): void {
    const ranges = this.gridApi.getCellRanges();
    const newRanges: Array<CellRangeParams> = [];
    // Check all ranged and return new ranges without the one specified
    ranges.forEach(range => {
      let startRow = Math.min(range.startRow.rowIndex, range.endRow.rowIndex);
      let endRow = Math.max(range.startRow.rowIndex, range.endRow.rowIndex);
      if(rowToDeselect === startRow) {
        startRow++;
      } else if(rowToDeselect === endRow) {
        endRow--;
      } else if(rowToDeselect > startRow && rowToDeselect < endRow ) {
        // Split the range :
        newRanges.push({
          rowStartIndex: startRow,
          rowEndIndex: rowToDeselect-1,
          columns: range.columns
        });
        // Set startRow for the second splitted part :
        startRow = rowToDeselect+1;
      }
      if (startRow <=endRow) {
        newRanges.push({
          rowStartIndex: startRow,
          rowEndIndex: endRow,
          columns: range.columns
        });
      }
    });
    // Clean all range selection :
    this.gridApi.clearRangeSelection();
    // Add the new calculated ones :
    newRanges.forEach(range => this.gridApi.addCellRange(range));
  }

  /**
   *  Get all selected nodes from selection ranges.
   *
   * @returns
   */
  private getAllNodeFromRanges(): Array<CellNode> {
    const nodes: Array<CellNode> = [];
    const ranges = this.gridApi.getCellRanges();
    const rowModel = this.gridApi.getModel();
    ranges.forEach(range => {
      const startRow = Math.min(range.startRow.rowIndex, range.endRow.rowIndex);
      const endRow = Math.max(range.startRow.rowIndex, range.endRow.rowIndex);
      for (let rowIndex = startRow; rowIndex <= endRow; rowIndex++) {
        range.columns.forEach(column => {
          const rowNode = rowModel.getRow(rowIndex);
          nodes.push({
            rowNode,
            column
          });
        });
      }
    });
    return nodes;
  }

  private generateStaticColumns(): Array<ColDef> {
    return [{
        width: 135,
        enableCellChangeFlash: true,
        headerName: 'Date de dbut',
        field: 'startDate',
        cellClass: 'matrix-unselectable-cell',
        valueFormatter: (params: ValueFormatterParams) => DateHelperService.toShortDate(params.value)
      },
      {
        width: 125,
        enableCellChangeFlash: true,
        cellClass: 'matrix-unselectable-cell',
        headerName: 'Date de fin',
        field: 'endDate',
        valueFormatter: (params: ValueFormatterParams) => DateHelperService.toShortDate(params.value)
      },
      {
        width: 100,
        enableCellChangeFlash: true,
        cellClass: 'matrix-unselectable-cell',
        headerName: 'Semaine',
        field: 'weekNumber'
      }
    ] as Array<ColDef>;
  }

  private onCellEdited(data: RowModel, colId: string, newValue: number) {
    // Get the date :
    const date = data[colId].date;
    // Emit the new values :
    this.requirementPlanningChange.emit({date, newValue});
  }

  public applyGroupValues() {
    this.getAllNodeFromRanges().forEach(node => {
      const colId = node.column.getColId();
      const groupValue = this.groupValues[colId];
      const newData = this.updateDataValue(node.rowNode.data, colId, groupValue);
      // Update data
      node.rowNode.setData(newData);
      // Send updated data to controller :
      this.onCellEdited(newData, colId, groupValue);
    });
    // Recompute checkbox states after grid refresh (as they are re-init)
    this.updateCheckboxStates();
  }


  private onSelectionModeChange(mode: MatrixSelectionMode) {
    switch (mode) {
      case 'all':
        this.selectAllRows();
        break;
      case 'none':
        this.gridApi.clearRangeSelection();
        break;
      case 'pair':
        this.selectPairWeekRows();
        break;
      case 'impair':
        this.selectImpairWeekRows();
      break;
      default:
        break;
    }
  }

}


