import { identifier, serializable } from 'serializr';
import { ObjectHelper } from '../../helpers/ObjectHelper';


export class JobTitle {

    @serializable(identifier())
    public id: number;

    @serializable
    public name_fr: string;

    @serializable
    public abbreviation_fr: string;

    constructor(init: Partial<JobTitle>) {
        if(init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getName(langCode: string): string {
        return this['name_' + langCode];
    }

    public getAbbreviation(langCode: string): string {
        return this['abbreviation_' + langCode];
    }

    public getFullName(langCode: string) {
        return `[${ this.getAbbreviation(langCode) }] ${ this.getName(langCode) }`;
    }

}
