import { Shift } from './../../models/planning/shift.model';
import { ResourceAffectation } from './../../models/planning/resource-affectation.model';
import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

export type ResourceAffectationsState = EntityState<ResourceAffectation>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'resource-affectations' })
export class ResourceAffectationsStore extends EntityStore<ResourceAffectationsState> {

  constructor() {
    super();
  }

  public setResourceAffectationFreeze(resourceAffectionId: ResourceAffectation['id'], isFrozen: boolean): void {
    this.update(resourceAffectionId, entity => {
      const newEntity = new ResourceAffectation(entity);
      newEntity.setFrozen(isFrozen);
      return newEntity;
    });
  }

  public setResourceAffectationDesire(resourceAffectionId: ResourceAffectation['id'], isDesired: boolean): void {
    this.update(resourceAffectionId, entity => {
      const newEntity = new ResourceAffectation(entity);
      newEntity.setIsDesired(isDesired);
      return newEntity;
    });
  }

  public setResourceAffectationPriority(resourceAffectionId: ResourceAffectation['id'], isPriority: boolean): void {
    this.update(resourceAffectionId, entity => {
      const newEntity = new ResourceAffectation(entity);
      newEntity.setIsPriority(isPriority);
      return newEntity;
    });
  }

  public removeResourceAffectation(resourceAffectationId: number): void {
    this.remove(resourceAffectationId);
  }

  public updateResourceShift(resourceAffectionId: ResourceAffectation['id'], shiftId: Shift['id']) {
    this.update(resourceAffectionId, entity => {
      const newEntity = new ResourceAffectation(entity);
      newEntity.setShiftId(shiftId);
      // Reset status of shift :
      newEntity.setFrozen(false);
      newEntity.setIsDesired(false);
      return newEntity;
    });
  }

}
