import { AppLocalesService } from './../../core/transloco/app-locales.service';
import { JobTitlesQuery } from './../job-titles/job-titles.query';
import { PersonsQuery } from './../persons/persons.query';
import { Person } from './../../models/person.model';
import {
    PlanningEditionContextHelper,
    PlanningSortingAttribute,
    PlanningSortingOrder
} from './../../models/planning/planning-edition-context.model';
import { Query } from '@datorama/akita';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { PlanningEditionContextStore } from './planning-edition-context.store';
import { PlanningEditionContext } from '../../models/planning/planning-edition-context.model';
import { map, switchMap } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class PlanningEditionContextQuery extends Query<PlanningEditionContext> {

    constructor(
        protected store: PlanningEditionContextStore,
        private personsQuery: PersonsQuery,
        private appLocalesService: AppLocalesService,
        private jobTitlesQuery: JobTitlesQuery) {
        super(store);
    }

    public selectCurrentCellContentType(): Observable<PlanningEditionContext['cellContentType']> {
        return this.select('cellContentType');
    }

    public selectCurrentPlanningType(): Observable<PlanningEditionContext['planningType']> {
        return this.select('planningType');
    }

    public selectCurrentZoomMode(): Observable<PlanningEditionContext['zoomMode']> {
        return this.select('zoomMode');
    }

    public selectCurrentVisualizationPeriod(): Observable<PlanningEditionContext['visualizationPeriod']> {
        return this.select('visualizationPeriod');
    }

    public selectCurrentSortingOrder(): Observable<PlanningEditionContext['sortingOrder']> {
        return this.select('sortingOrder');
    }

    public selectCurrentSortingAttribute(): Observable<PlanningEditionContext['sortingAttribute']> {
        return this.select('sortingAttribute');
    }

    public selectCurrentSortingData(): Observable<{
        sortingAttribute: PlanningSortingAttribute;
        sortingOrder: PlanningSortingOrder;
    }> {
        return this.selectCurrentSortingAttribute().pipe(
            switchMap(
                sortingAttribute => this.selectCurrentSortingOrder().pipe(
                    map(sortingOrder =>
                        ({
                            sortingAttribute,
                            sortingOrder
                       })
                    )
                )
            )
          );
    }

    public compareResources(planningResource1: Person['id'], planningResource2: Person['id']): number {
        const currentContext = this.getValue();
        const langCode = this.appLocalesService.getCurrentAppLanguageCode();
        const resource1 = this.personsQuery.getEntity(planningResource1);
        const resource2 = this.personsQuery.getEntity(planningResource2);
        const jobTitle1 = this.jobTitlesQuery.getEntity(resource1.getJobTitleId());
        const jobTitle2 = this.jobTitlesQuery.getEntity(resource2.getJobTitleId());
        const value1 =
            PlanningEditionContextHelper.getSortingAttributeValue(currentContext.sortingAttribute, resource1, jobTitle1, langCode);
        const value2 =
            PlanningEditionContextHelper.getSortingAttributeValue(currentContext.sortingAttribute, resource2, jobTitle2, langCode);
        const order = currentContext.sortingOrder === 'asc' ? 1 : -1;
        return (value1 ? value1.localeCompare(value2) : -1 ) * order;
    }

    public getCurrentVisualizationPeriod(): PlanningEditionContext['visualizationPeriod'] {
        return this.getValue().visualizationPeriod;
    }

    public isPeriodicPlanning(): Observable<boolean> {
        return this.selectCurrentCellContentType().pipe(
            map(type => type === 'shifts')
        );
    }

    public isPlannedPlanning(): Observable<boolean> {
        return this.selectCurrentPlanningType().pipe(
            map(type => type === 'planned')
        );
    }
}
