import { PlanningEditionContextStore } from 'src/app/common//store/planning-edition-context/planning-edition-context.store';
import { PlanningEditionContextQuery } from 'src/app/common/store/planning-edition-context/planning-edition-context.query';
import {
  PlanningZoomMode, PlanningSortingOrder, PlanningSortingAttribute
} from 'src/app/common/models/planning/planning-edition-context.model';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Subscription } from 'rxjs';



@Component({
  selector: 'epop-planning-period-dialog',
  templateUrl: './resource-header-params-dialog.component.html',
  styleUrls: ['./resource-header-params-dialog.component.scss'],
})
export class ResourceHeaderParamsDialogComponent implements OnInit, OnDestroy {

  private subscriptions: Array<Subscription> = [];
  public zoomModes: Array<PlanningZoomMode>= ['normal', 'compacted', 'ultraCompact'];

  public sortingModes: Array<PlanningSortingAttribute>= [
    'byFirstName',
    'byLastName',
    'byFunction',
  ];

  public currentZoomMode: PlanningZoomMode;
  public currentSortingOrder: PlanningSortingOrder;
  public currentSortingAttribute: PlanningSortingAttribute;

  constructor(
    public dialogRef: MatDialogRef<ResourceHeaderParamsDialogComponent>,
    private planningEditionContextQuery: PlanningEditionContextQuery,
    private planningEditionContextStore: PlanningEditionContextStore,
    ) { }

  ngOnInit(): void {
    this.subscriptions.push(
      this.planningEditionContextQuery.selectCurrentZoomMode().subscribe(zoomMode => {
        this.currentZoomMode = zoomMode;
      }),
      this.planningEditionContextQuery.selectCurrentSortingAttribute().subscribe(attr => {
        this.currentSortingAttribute = attr;
      }),
      this.planningEditionContextQuery.selectCurrentSortingOrder().subscribe(sorting => {
        this.currentSortingOrder = sorting;
      })
    );
  }

  public ngOnDestroy(): void {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  public onSaveExit() {
      this.dialogRef.close();
  }

  public onZoomModeChanged() {
    this.planningEditionContextStore.setZoomMode(this.currentZoomMode);
  }

  public onSortingChanged(sortAttr: PlanningSortingAttribute) {
    if(this.currentSortingAttribute === sortAttr) {
      // Change filter order
      this.currentSortingOrder = this.currentSortingOrder === 'asc' ? 'desc' : 'asc';
    } else {
      // Reset filter order :
      this.currentSortingOrder = 'asc';
      // Update filter attribute :
      this.currentSortingAttribute = sortAttr;
    }
    this.planningEditionContextStore.setSortingMode(this.currentSortingAttribute, this.currentSortingOrder);
  }

}
