import { PlanningConstraintsStore } from 'src/app/common/store/planning-constraints/planning-constraint.store';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { EpopService } from './abstract-epop.service';
import { PlanningConstraint } from '../../models/planning/planning-constraint.model';

@Injectable({
    providedIn: 'root'
})
export class ConstraintService extends EpopService {

    constructor(
        httpClient: HttpClient,
        private planningConstraintsStore: PlanningConstraintsStore
    ) {
        super(httpClient);
    }

    public getResourcePath(): string {
        return 'constrains';
    }

    /**
     * Update constraints params
     */
     public updateConstraintParamValue(constraintId: number, newValue: PlanningConstraint['params']): void {
        this.planningConstraintsStore.setConstraintParamValue(Number(constraintId), newValue);
    }

    /**
     * Update constraints state (enable/disable)
     */
     public updateConstraintStateValue(constraintId: number, isEnable: boolean): void {
        this.planningConstraintsStore.setConstraintStateValue(Number(constraintId), isEnable);
    }

}
