import { OverlayContainer, FullscreenOverlayContainer } from '@angular/cdk/overlay';
import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'epop-app-dialog',
  templateUrl: './app-dialog.component.html',
  styleUrls: ['./app-dialog.component.scss'],
  providers: [
    { provide: OverlayContainer, useClass: FullscreenOverlayContainer },
  ]
})
export class AppDialogComponent implements OnInit {

  @Input()
  public dialogTitle: string;

  @Input()
  public dialogSize: 'very-small' |'small' |'medium' | 'big' | 'auto' = 'auto';

  @Input()
  public customSize: number;

  @Input()
  public alignement: 'left' |'center' = 'left';

  @Input()
  public dialogColor: string;

  constructor() { }

  public ngOnInit(): void {
  }

  public getDialogClass(): string {
    return `epop-app-dialog epop-app-dialog--${this.dialogSize} epop-app-dialog--${this.alignement}`;
  }

}
