import { ResourceDayAffectationsStore } from './../resource-day-affectations/resource-day-affectations.store';
import { PersonsStore } from '../persons/persons.store';
import { TeamPlanningsStore } from 'src/app/common/store/team-plannings/team-plannings.store';
import { ResourcePlanningsStore } from '../resource-plannings/resource-plannings.store';
import { ResourceAffectationsStore } from '../resource-affectations/resource-affectations.store';
import { Injectable } from '@angular/core';
import { EpopNormalizedData } from '../../services/epop-api/epop-normalized-model.definition';

/**
 * Helper to feed all related stores with received Epop data
 */
@Injectable({ providedIn: 'root' })
export class EpopStoreHelper {

    constructor(private resourceAffectationsStore: ResourceAffectationsStore,
                private resourceDayAffectationsStore: ResourceDayAffectationsStore,
                private resourcePlanningsStore: ResourcePlanningsStore,
                private teamPlanningsStore: TeamPlanningsStore,
                private personsStore: PersonsStore) {}

    /**
     * Update all epop stores with normalized data.
     *
     * @param data
     */
    setNormalizedData(data: EpopNormalizedData) {
        this.resourceAffectationsStore.set(data.entities.resourceAffectations);
        this.resourceDayAffectationsStore.set(data.entities.resourceDayAffectations);
        this.resourcePlanningsStore.set(data.entities.resourcePlannings);
        this.teamPlanningsStore.set(data.entities.teamPlannings);
        this.personsStore.set(data.entities.persons);
    }

}
