import { Component, OnInit } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { LayoutPage } from '../../main-layout/models/layout-page.model';
import { MainLayoutStore } from '../../main-layout/store/main-layout.store';
import { PlanningGeneratorRouteResolver } from '../planning-generator-routing.resolver';

@Component({
    selector: 'epop-units-page',
    styleUrls: ['units.page.scss'],
    templateUrl: 'units.page.html'
})
export class UnitsPage extends LayoutPage implements OnInit {

    constructor(
        private translocoService: TranslocoService,
        mainLayoutStore: MainLayoutStore,
        private router: Router
    ) {
        super(mainLayoutStore);
     }

    ngOnInit(): void {
        this.subscribePageTitle(this.translocoService.selectTranslate('units.title', {}, 'planningGenerator'));
    }

    public unitSelected(unitId: number) {
        const path = PlanningGeneratorRouteResolver.routePath('unit', unitId.toString());
        this.router.navigateByUrl(path);
    }
}
