import { Component, OnInit, ContentChildren, QueryList, TemplateRef, AfterContentInit } from '@angular/core';
import { AppPageContainerPageDirective } from './app-page-container.directives';

@Component({
  selector: 'epop-app-page-container',
  templateUrl: './app-page-container.component.html',
  styleUrls: ['./app-page-container.component.scss']
})
export class AppPageContainerComponent {

  private currentPage = 0;

  @ContentChildren(AppPageContainerPageDirective)
  public pageChildren: QueryList<AppPageContainerPageDirective>;

  constructor() { }

  public getCurrentPage(): number {
    return this.currentPage;
  }

  public goToNextPage(): void {
    if (this.currentPage < this.pageChildren.length - 1) {
      this.gotToPage(this.currentPage + 1);
    }
  }

  public goToPreviousPage(): void {
    if (this.currentPage > 0) {
      this.gotToPage(this.currentPage - 1);
    }
  }

  public gotToPage(pageNumber: number): void {
    this.currentPage = pageNumber;
  }

}
