import { ConstraintService } from './../../../../common/services/epop-api/constraint.service';
import { PlanningConstraintsStore } from 'src/app/common/store/planning-constraints/planning-constraint.store';
import { PlanningConstraintCategory } from 'src/app/common/models/planning/planning-constraint-category.model';
import { PlanningConstraintsQuery } from './../../../../common/store/planning-constraints/planning-constraint.query';
import { Subscription } from 'rxjs';
import { Component, OnInit, OnDestroy, OnChanges, SimpleChanges, ChangeDetectionStrategy } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import {
  PlanningConstraintCategoriesQuery
} from 'src/app/common/store/planning-constraint-categories/planning-constraint-categories.query';
import { TranslocoService } from '@ngneat/transloco';
import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { TextInputChangeEvent } from 'src/app/common/components/multi-input-text/components/multi-input-text/multi-input-text.component';
import { StringHelper } from 'src/app/common/helpers/StringHelper';
import { contrainstListMock } from 'src/app/common/mocks/epop-api/planning.mock';

const MIN_SEARCH_LENGTH = 2;

@Component({
  selector: 'epop-constraint-list-dialog',
  templateUrl: './constraint-list-dialog.component.html',
  styleUrls: ['./constraint-list-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ConstrainstListDialogComponent implements OnInit, OnDestroy {

  public constraintSearchText = '';

  private subscriptions: Array<Subscription> = [];

  public constraintsList: Record<PlanningConstraintCategory['id'],Array<PlanningConstraint>> = {};
  public constraintCategoriesList: Array<PlanningConstraintCategory> = [];
  public updatedConstraintParamList: Record<number, PlanningConstraint['params']> = [];
  public updatedConstraintStateList: Record<number, boolean> = [];

  public filteredConstraintsList: Record<PlanningConstraintCategory['id'], Array<PlanningConstraint>> = [];
  public filteredCategoriesList: Array<PlanningConstraintCategory> = [];

  constructor(
    public dialogRef: MatDialogRef<ConstrainstListDialogComponent>,
    public planningConstraintsQuery: PlanningConstraintsQuery,
    public planningConstraintCategoriesQuery: PlanningConstraintCategoriesQuery,
    private planningConstraintsService: ConstraintService,
    private translocoService: TranslocoService
    ) { }

  ngOnInit(): void {
    // Get list of constraints :
    this.subscriptions.push( this.planningConstraintCategoriesQuery.selectAllWithConstaints().subscribe(l => this.constraintsList = l));
    this.subscriptions.push(this.planningConstraintCategoriesQuery.selectAll().subscribe(l => this.constraintCategoriesList = l));
  }

  public ngOnDestroy(): void {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  public onFilterChanged(): void {
    if(this.constraintSearchText && this.constraintSearchText.length > MIN_SEARCH_LENGTH) {
      // Filter constraints :
      for(const catId in this.constraintsList) {
        if (Object.prototype.hasOwnProperty.call(this.constraintsList, catId)) {
          this.filteredConstraintsList[catId] =
            this.constraintsList[catId].filter(e => StringHelper.matchTextSearch(e, this.constraintSearchText));
        }
      }

      // Filter categories (if not empty)
      this.filteredCategoriesList = this.constraintCategoriesList.
            filter(c => this.filteredConstraintsList[c.getId()].length > 0);
      } else {
        this.filteredConstraintsList = {...this.constraintsList};
        this.filteredCategoriesList = this.constraintCategoriesList;
      }
  }

  public getConstraintList(category: PlanningConstraintCategory): Array<PlanningConstraint> {
    return this.filteredConstraintsList[category.getId()];
  }

  public getParameterList(constraintElement: PlanningConstraint) {
    return [
      {
        label: this.translocoService.translate('planningGenerator.planning.model.planningConstrainst.filtre'),
        value : constraintElement.getFiltre()
      },
      {
        label: this.translocoService.translate('planningGenerator.planning.model.planningConstrainst.weight'),
        value : constraintElement.getWeight()
      },
      {
        label: this.translocoService.translate('planningGenerator.planning.model.planningConstrainst.period'),
        value: this.getPeriodLabel(constraintElement),
      },
    ];
  }

  private getPeriodLabel(constraintElement: PlanningConstraint): string {
    const periodLabels = constraintElement.getPeriod().map((date) => date && DateHelperService.toShortDate(date));
    if(periodLabels[0] !== undefined && periodLabels[1] !== undefined) {
      return this.translocoService.translate(
        'common.date.period',
        { startDate: periodLabels[0], endDate: periodLabels[1] }
      );
    } else {
      return undefined;
    }
  }

  public onTextInputChange(constraint: PlanningConstraint, newValue: TextInputChangeEvent): void {
    const newParamValue = this.updatedConstraintParamList[constraint.getId()] ?? {};
    newParamValue[newValue.inputKey]= newValue.value;
    this.updatedConstraintParamList[constraint.getId()] = newParamValue;
  }

  public isItemEnable(constraint: PlanningConstraint) {
    const overridenValue = this.updatedConstraintStateList[constraint.getId()];
    return overridenValue !== undefined ? overridenValue : constraint.getIsEnable();
  }

  public onConstraintStateChanged(constraint: PlanningConstraint, event: Event): void {
    const element = event.target as HTMLInputElement;
    this.updatedConstraintStateList[constraint.getId()] = element.checked;
  }

  public onSaveExit() {
    // Constraint params updates
    for(const constraintId in this.updatedConstraintParamList) {
      if(this.updatedConstraintParamList.hasOwnProperty(constraintId)) {
        const newValues = this.updatedConstraintParamList[constraintId];
        this.planningConstraintsService.updateConstraintParamValue(Number(constraintId), newValues);
      }
    }
    // Constraint state updates :
    for(const constraintId in this.updatedConstraintStateList) {
      if(this.updatedConstraintStateList.hasOwnProperty(constraintId)) {
        const newValue = this.updatedConstraintStateList[constraintId];
        this.planningConstraintsService.updateConstraintStateValue(Number(constraintId), newValue);
      }
    }
    this.dialogRef.close();
  }

}
