import { ITextMatchableObject } from './../../helpers/StringHelper';
import { PlanningConstraintCategory } from './planning-constraint-category.model';
import { list, map, serializable, primitive, date } from 'serializr';
import { ObjectHelper } from '../../helpers/ObjectHelper';

export class PlanningConstraint implements ITextMatchableObject{

    @serializable
    public id: number;

    @serializable
    public label?: string;

    @serializable
    public description: string;

    @serializable
    public category: PlanningConstraintCategory['id'];

    @serializable
    public filtre: string;

    @serializable
    public weight: number;

    @serializable(date())
    public startPeriod: Date;

    @serializable(date())
    public endPeriod: Date;

    @serializable(map(primitive()))
    public params: Record<string, number>;

    @serializable
    public isMandatory: boolean;

    @serializable
    public isEnable: boolean;


    constructor(init: Partial<PlanningConstraint>) {
        if(init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): number {
        return this.id;
    }

    public getCategoryId(): number {
        return this.category;
    }

    public getSearchableText(): string {
        const text: string = this.label +
                this.description +
                this.filtre;
        return text;
    }

    public getPeriod(): [Date, Date] {
        return [this.startPeriod, this.endPeriod];
    }

    public getWeight(): number {
        return this.weight;
    }

    public getFiltre(): string {
        return this.filtre;
    }

    public getLabel(): string {
        return this.label;
    }

    public getDescription(): string {
        return this.description;
    }

    public getIsMandatory(): boolean {
        return this.isMandatory;
    }

    public getIsEnable(): boolean {
        return this.isEnable;
    }

    public getParams(): PlanningConstraint['params'] {
        return this.params;
    }

    public setIsEnable(isEnable: boolean) {
        this.isEnable = isEnable;
    }

    public setParams(newParamKey: string, newParamValue: number) {
        this.params[newParamKey] = newParamValue;
    }

}
