import { UnitDetailsQuery } from 'src/app/common/store/unitDetails/unit-details.query';
import {
  EventShiftListDialogComponent,
  EventShiftListDialogData
} from './components/shift-list-dialog/components/event-shift-dialog/event-list-dialog.component';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Shift } from 'src/app/common/models/planning/shift.model';
import { Observable } from 'rxjs';
import { PlanningEditionContextQuery } from 'src/app/common/store/planning-edition-context/planning-edition-context.query';
import { map } from 'rxjs/operators';
import { MatDialog } from '@angular/material/dialog';
import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import {
  ShiftListDialogData,
  TimeShiftListDialogComponent
} from './components/shift-list-dialog/components/time-shift-dialog/shift-list-dialog.component';
import { MatAccordion, MatExpansionPanel } from '@angular/material/expansion';

@Component({
  selector: 'epop-shift-list',
  templateUrl: './shift-list.component.html',
  styleUrls: ['./shift-list.component.scss']
})
export class ShiftListComponent implements OnInit {

  public shiftList: Observable<Shift[]>;

  public eventList: Observable<EventShift[]>;

  public isShiftDisable: Observable<boolean>;

  @Input()
  unitId: number;

  constructor(
        private unitDetailsQuery: UnitDetailsQuery,
        private dialog: MatDialog,
        private planningEditionContextQuery: PlanningEditionContextQuery) { }

  ngOnInit(): void {
    this.shiftList = this.unitDetailsQuery.getFavoriteShiftList(this.unitId);
    this.eventList = this.unitDetailsQuery.getFavoriteEventShiftList(this.unitId);
    this.isShiftDisable = this.planningEditionContextQuery.selectCurrentCellContentType().pipe(
      map(event => event === 'onlyEvents')
    );
  }

  public openEventListDialog(): void {
    this.dialog.open(EventShiftListDialogComponent, {
      data: {
        unitId: this.unitId
      } as EventShiftListDialogData
    });
  }

  public openShiftListDialog(): void {
    this.dialog.open(TimeShiftListDialogComponent, {
      data: {
        unitId: this.unitId
      } as ShiftListDialogData
    });
  }

}
