import { Component } from '@angular/core';
import { MatCheckboxChange } from '@angular/material/checkbox';

export type MatrixSelectionMode = 'none' | 'all' | 'pair' | 'impair';

export type RequirementMatrixSelectionHeaderParams = {
  onSelect: (mode: MatrixSelectionMode) => void;
  onReady: (component: RequirementMatrixSelectionHeaderComponent) => void;
};

@Component({
  selector: 'epop-requirement-matrix-selection-headers',
  templateUrl: './requirement-matrix-selection-header.component.html',
  styleUrls: ['./requirement-matrix-selection-header.component.scss']
})
export class RequirementMatrixSelectionHeaderComponent {

  public currentMode: MatrixSelectionMode = 'none';

  public params: RequirementMatrixSelectionHeaderParams;

  public checkboxState: boolean;

  agInit(params: RequirementMatrixSelectionHeaderParams): void {
    this.params = params;
    this.params.onReady(this);
  }

  public onClickCheck(event: MatCheckboxChange) {
    const newMode: MatrixSelectionMode = event.checked ? 'all' : 'none';
    this.selectMode(newMode);
  }

  public selectMode(mode: MatrixSelectionMode) {
    this.currentMode = mode;
    this.params.onSelect(mode);
  }

  public resetCheckboxState() {
    this.currentMode = 'none';
      this.checkboxState = false;
  }
}
