import { TranslocoService } from '@ngneat/transloco';
import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { TeamPlanningsQuery } from 'src/app/common/store/team-plannings/team-plannings.query';
import { PlanningEditionContextQuery } from 'src/app/common/store/planning-edition-context/planning-edition-context.query';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { PlanningEditionContextStore } from 'src/app/common/store/planning-edition-context/planning-edition-context.store';
import { PlanningPeriodDialogComponent } from '../planning-period-dialog/planning-period-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { PlanningEditionContext } from 'src/app/common/models/planning/planning-edition-context.model';
import { ActionSelectionOptionGroup } from 'src/app/common/components/action-select/action-select.component';

@Component({
  selector: 'epop-planning-header',
  templateUrl: './planning-header.component.html',
  styleUrls: ['./planning-header.component.scss']
})
export class PlanningHeaderComponent implements OnInit, OnDestroy {

  public isPeriodicPlanning: boolean;
  public isPlannifiedPlanning: boolean;
  public currentPlanningDates: [Date, Date];
  public currentVisuMode: PlanningEditionContext['visualizationPeriod'];
  public visuModesGroups: Array<Array<PlanningEditionContext['visualizationPeriod']>> =
    [
      [
        'normal',
        'compacted'
      ],
      [
        'week',
        '2weeks',
        '3weeks',
        '4weeks'
      ]
    ];
  public visuModeGroups: ActionSelectionOptionGroup = [];
  private subscriptions: Array<Subscription> = [];

  constructor(
    private planningEditionContextStore: PlanningEditionContextStore,
    private planningEditionContextQuery: PlanningEditionContextQuery,
    private teamPlanningsQuery: TeamPlanningsQuery,
    private translocoService: TranslocoService,
    private dialog: MatDialog) {}

  ngOnInit(): void {
    this.subscriptions.push(
      this.planningEditionContextQuery.isPlannedPlanning().subscribe(type => this.isPlannifiedPlanning = type),
      this.planningEditionContextQuery.isPeriodicPlanning().subscribe(type => this.isPeriodicPlanning = type),
      this.teamPlanningsQuery.selectActivePlanningPeriodDates().subscribe(period => this.currentPlanningDates = period),
      this.planningEditionContextQuery.selectCurrentVisualizationPeriod().subscribe(visuMode => {
        this.currentVisuMode = visuMode;
      })
    );

    this.visuModeGroups = this.visuModesGroups.map(groupMode => groupMode.map( mode => ({
      value: mode,
      label: this.translocoService.translate('planningGenerator.planning.model.visualization.modes.' + mode)
    })));
  }

  onChangePlanningCellType() {
    this.planningEditionContextStore.setCellContentType(this.isPeriodicPlanning ? 'shifts' : 'onlyEvents');
  }

  onChangePlanningType() {
    this.planningEditionContextStore.setPlanningType(this.isPlannifiedPlanning ? 'onPreparation' : 'planned');
  }

  public getPeriodLabel(): Observable<string> {
    if(this.currentPlanningDates !== undefined) {
      const periodLabels = this.currentPlanningDates.map((date) => DateHelperService.toShortDate(date));
      return this.translocoService.selectTranslate(
        'common.date.period',
        { startDate: periodLabels[0], endDate: periodLabels[1] }
      );
    }
  }

  public onVisuModeChanged() {
    // Use of timeout to have a delay during dropdown closing animation and prevent lags.
    setTimeout(() => {
      this.planningEditionContextStore.setVisualizationPeriod(this.currentVisuMode);
    }, 50);
  }

  public openPlanningPeriodDialog(): void {
    this.dialog.open(PlanningPeriodDialogComponent);
  }

  public ngOnDestroy(): void {
    this.subscriptions.forEach(sub => sub.unsubscribe());
    this.subscriptions = [];
  }

}
