import { serializable } from 'serializr';
import { ObjectHelper } from '../../helpers/ObjectHelper';

export class PlanningConstraintCategory {

    @serializable
    public id?: number;

    @serializable
    public categoryLabel?: string;

    constructor(init: Partial<PlanningConstraintCategory>) {
        if(init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): number {
        return this.id;
    }

    public getCategoryLabel(): string {
        return this.categoryLabel;
    }

}
