import { UnitService } from 'src/app/common/services/epop-api/units.service';
import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import { AbstractShift } from './../../../../../../../../common/models/planning/abstract-shift.model';
import { AppPageContainerComponent } from './../../../../../../../../common/components/app-page-container/app-page-container.component';
import { UnitDetailsQuery } from 'src/app/common/store/unitDetails/unit-details.query';
import { UnitDetails } from 'src/app/common/models/unit-details.model';
import { EventShiftsStore } from './../../../../../../../../common/store/event-shifts/event-shifts.store';
import { Shift } from 'src/app/common/models/planning/shift.model';
import { Subscription } from 'rxjs';
import { Component, OnInit, OnDestroy, Inject, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TranslocoService } from '@ngneat/transloco';
import { ColDef } from 'ag-grid-community';
import { ShiftColorChangedEvent } from '../shift-grid/shift-grid.component';
import { UnitDetailsStore } from 'src/app/common/store/unitDetails/unit-details.store';
import { ShiftMock } from 'src/app/common/mocks/epop-api/shift.mock';

export type EventShiftListDialogData = {
  unitId: UnitDetails['id'];
};

@Component({
  selector: 'epop-event-shift-list-dialog',
  templateUrl: './event-list-dialog.component.html',
  styleUrls: ['./event-list-dialog.component.scss']
})
export class EventShiftListDialogComponent implements OnInit, OnDestroy {

  private subscriptions: Array<Subscription> = [];
  private shiftToDelete: Array<EventShift['id']> = [];
  public initialShifts: Array<EventShift> = [];
  public shiftList: Array<EventShift>;
  public eventDictionnaryList: Array<EventShift>;
  public eventSearchText: string;

  @ViewChild(AppPageContainerComponent)
  public pageContainerComponent: AppPageContainerComponent;

  // Column definition :
  public additionalColumnDefs: Array<ColDef> = [
    {
      headerName: this.translocoService.translate('shift.description'),
      field: 'description',
      flex: 1,
      cellClass: 'event-list-grid__col__description',
      sortable: true
    }
  ];
  constraintSearchText: string;

  constructor(
    public dialogRef: MatDialogRef<EventShiftListDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: EventShiftListDialogData,
    private translocoService: TranslocoService,
    private unitDetailsQuery: UnitDetailsQuery,
    private unitService: UnitService,
    private shiftService: ShiftMock
    ) { }

  public ngOnInit(): void {
    // Get list of event shifts :
    this.subscriptions.push(
        this.unitDetailsQuery.getFavoriteEventShiftList(this.data.unitId)
          .subscribe(shiftList => this.shiftList = this.initialShifts = shiftList)
    );
    // Get list of event shifts dictionnary :
    this.subscriptions.push(
      this.shiftService.getAllEventShifts()
        .subscribe(shiftList => this.eventDictionnaryList = shiftList)
    );
  }

  public ngOnDestroy(): void {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  public onShiftDeleted(shiftId: Shift['id']) {
    // Delete the shift on save :
    this.shiftToDelete.push(shiftId);
    // Remove the shift from the table rows :
    this.shiftList = this.shiftList.filter(shift => shift.getId() !== shiftId);
  }

  public onSaveExit() {
    // Set new shit list :
    this.unitService.udapteFavoriteEventShiftList(this.data.unitId, this.shiftList);
    this.dialogRef.close();
  }

  public onShiftColorChanged(changeEvent: ShiftColorChangedEvent, changeType: ('text' | 'background')): void {
    const updatedShiftIndex = this.shiftList.findIndex(s => s.getId() === changeEvent.id);
    const updatedShift = new EventShift(this.shiftList[updatedShiftIndex] as any);
    this.shiftList[updatedShiftIndex] = updatedShift;
    if(changeType === 'text') {
      updatedShift.setRgbTextColor(changeEvent.color);
    } else {
      updatedShift.setBackgroundColor(changeEvent.color);
    }
  }

  public goToNewEventPage(): void {
    this.pageContainerComponent.goToNextPage();
  }

  public goToListEventPage(): void {
    this.pageContainerComponent.goToPreviousPage();
  }

  public onShiftsSelected(selectedShifts: Array<AbstractShift>): void {
    const newShifts = [];
    const updatedShifs = [];
    selectedShifts.forEach(selected => {
      const alreadySelected = this.shiftList.find(s => s.getId() === selected.getId());
      if(alreadySelected) {
        updatedShifs.push(alreadySelected);
      } else {
        newShifts.push(selected);
      }
    });
    this.shiftList = [...updatedShifs, ...newShifts];
  }

}
