import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

export type TextInputChangeEvent = {inputKey: string; value: number};

export type MultiInputTextParams = {
  text: string;
  defaultValues: Record<string, number>;
};

type RenderElement = {
  type: 'input' | 'text';
  key: string;
};


@Component({
  selector: 'epop-multi-input-text',
  templateUrl: './multi-input-text.component.html',
  styleUrls: ['./multi-input-text.component.scss']
})
export class MultiInputTextComponent implements OnInit {

  @Input()
  multiInputparams: MultiInputTextParams;

  @Output()
  textInputChange: EventEmitter<TextInputChangeEvent> = new EventEmitter();

  renderElements: Array<RenderElement>;

  constructor() { }

  ngOnInit(): void {
    this.getRenderElements();
  }

  getRenderElements() {
    this.renderElements = [];
    const regex = /{\w*}/g;
    const stringPattern = this.multiInputparams.text.split(regex);
    const stringInputs = this.multiInputparams.text.match(regex);
    for(let i = 0; i < stringPattern.length; i++) {
      this.renderElements.push(
        {
          type: 'text',
          key: stringPattern[i]
        });
        if(stringInputs && i < stringInputs.length) {
          this.renderElements.push( {
            type: 'input',
            key: stringInputs[i].slice(1, -1) // slice bracets to only get variable name
          });
        }
    }
  }

  public onInputChanged(renderElmt: RenderElement, event: any): void {
    this.textInputChange.emit({
      inputKey: renderElmt.key as string,
      value: Number(event.target.value as string)
    });
  }

}
