import { AppLocalesService } from '../../../../../../../../common/core/transloco/app-locales.service';
import { JobTitlesQuery } from 'src/app/common/store/job-titles/job-titles.query';
import { ShiftsQuery } from 'src/app/common/store/shifts/shifts.query';
import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { JobTitle } from 'src/app/common/models/planning/job-title.model';
import { Shift } from 'src/app/common/models/planning/shift.model';
import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';

@Component({
  selector: 'epop-requirement-list-formatter',
  templateUrl: './requirement-list-formatter.component.html',
  styleUrls: ['./requirement-list-formatter.component.scss']
})
export class RequirementListFormatterComponent implements OnInit {

  @Input()
  public requiredShiftIds: Array<number>;

  @Input()
  public requiredJobTitles: Array<number>;

  constructor(private shiftsQuery: ShiftsQuery, private jobTitlesQuery: JobTitlesQuery, private appLocalesService: AppLocalesService) { }

  ngOnInit(): void {
  }


  public getShiftRequirements(): Observable<Array<Shift>> {
    return this.shiftsQuery.selectMany(this.requiredShiftIds);
  }

  public getJobTitles(): Observable<Array<JobTitle>> {
    return this.jobTitlesQuery.selectMany(this.requiredJobTitles);
  }

  public getJobTitleLabel(jobTitle: JobTitle): string {
    return jobTitle.getAbbreviation(this.appLocalesService.getCurrentAppLanguageCode());
  }

}
