import { Shift } from 'src/app/common/models/planning/shift.model';
import { Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';

export type ShiftColorCellParams = {
  value: string; // color to show
  onColorChanged: (shiftId: number, color: string) => void;
  readOnly: boolean;
} & Omit<ICellRendererParams, 'value'>;

@Component({
  selector: 'epop-shift-color-cell',
  templateUrl: './shift-color-cell.component.html',
  styleUrls: ['./shift-color-cell.component.scss']
})
export class ShiftColorCellComponent implements ICellRendererAngularComp {
  public params: ShiftColorCellParams;
  public currentColor: string;
  public lastColor: string;


  constructor() {}

  agInit(params: ShiftColorCellParams): void {
    this.params = params;
    this.currentColor = params.getValue();
    this.lastColor = this.currentColor;
  }

  refresh(): boolean {
    // By returning false, we let the grid refresh the cell for us.
    return false;
  }

  public onColorChanged(): void {
    if(this.currentColor !== this.lastColor) {
      this.params.onColorChanged(this.getCurrentShift().getId(), this.currentColor);
      this.lastColor = this.currentColor;
    }
  }

  private getCurrentShift(): Shift {
    return this.params.data;
  }
}
