import { Shift } from './../../models/planning/shift.model';
import { NormalizedSchema, schema } from 'normalizr';
import { ResourceAffectation } from '../../models/planning/resource-affectation.model';
import { ResourcePlanning } from '../../models/planning/resource-planning.model';
import { TeamPlanning } from '../../models/planning/team-planning.model';
import { ResourceDayAffectation } from '../../models/planning/resource-day-affectation.model';
import { Person } from '../../models/person.model';

const defaultOption = {
    // processStrategy: strategy to use when creating the entity
    processStrategy: (value) => {
        if(value.constructor) {
            // allow to call constructor of original object if possible
            return new value.constructor(value);
        } else {
            // create a simple shallow copy
            return {...value};
        }
    },
    mergeStrategy: (entityA, entityB) => entityA
};

const shiftSchema = new schema.Entity<Shift>('shifts', {}, defaultOption);

const personSchema = new schema.Entity<Person>('persons', {}, defaultOption);

const resourceAffectationSchema = new schema.Entity<ResourceAffectation>('resourceAffectations', {
    shift: shiftSchema,
}, defaultOption);

const resourceDayAffectationSchema = new schema.Entity<ResourceDayAffectation>('resourceDayAffectations', {
    workAffectation: resourceAffectationSchema,
    dayEvent: resourceAffectationSchema,
    onDutyAffectation: resourceAffectationSchema
}, defaultOption);

const resourceAffectationsValues =
            new schema.Values(resourceDayAffectationSchema);

const resourcePlanningSchema = new schema.Entity<ResourcePlanning>('resourcePlannings', {
    planning: resourceAffectationsValues,
    resource: personSchema
}, defaultOption);

const teamPlanningSchema = new schema.Entity<TeamPlanning>('teamPlannings', {
    resourcePlannings: [resourcePlanningSchema],
}, defaultOption);

export const EpopSchema = {
    shiftSchema,
    personSchema,
    resourceAffectationSchema,
    resourcePlanningSchema,
    teamPlanningSchema
};


export type EpopNormalizedData = NormalizedSchema<{
    shifts: Record<Shift['id'], Shift>;
    persons: Record<Person['id'], Person>;
    resourceAffectations: Record<ResourceAffectation['id'], ResourceAffectation>;
    resourceDayAffectations: Record<ResourceDayAffectation['id'], ResourceDayAffectation>;
    resourcePlannings: Record<ResourcePlanning['id'], ResourcePlanning>;
    teamPlannings: Record<TeamPlanning['id'], TeamPlanning>;
}, number>;
