import { Component, forwardRef, Input, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

export type ActionSelectionOptionGroup = Array<Array<ActionSelectionOption>>;

export type ActionSelectionOption = {
  label: string;
  value: string;
};

@Component({
  selector: 'epop-action-select',
  templateUrl: './action-select.component.html',
  styleUrls: ['./action-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ActionSelectComponent),
      multi: true
    }
  ]
})
export class ActionSelectComponent implements OnInit, ControlValueAccessor {

  private controlValue: string;
  public get value(): string { return this.controlValue; }
  public set value(v: string) {
    if (v !== this.controlValue) {
      this.controlValue = v;
      this.onChange(v);
    }
  }

  @Input()
  optionGroups: ActionSelectionOptionGroup = [];

  constructor() { }

  ngOnInit(): void {
  }

  writeValue(value: any): void {
    this.value = value;
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  // Unused controlValueAccessor events :
  onChange = (_) => { };
  onTouched = () => { };

}
