import { ResourcePlanningsStore } from './../resource-plannings/resource-plannings.store';
import { ResourceAffectation } from './../../models/planning/resource-affectation.model';
import { TeamPlanning } from './../../models/planning/team-planning.model';
import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';
import { ResourcePlanning } from '../../models/planning/resource-planning.model';
import { ResourceAffectationsStore } from '../resource-affectations/resource-affectations.store';
import { DayShiftType } from '../../models/planning/resource-day-affectation.model';

export type TeamPlanningsState = EntityState<TeamPlanning>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'team-plannings' })
export class TeamPlanningsStore extends EntityStore<TeamPlanningsState> {
  constructor(private resourceAffectationsStore: ResourceAffectationsStore, private resourcePlanningsStore: ResourcePlanningsStore) {
    super();
  }

  /**
   * Add a new resource affection object to a resource planning. It will also add the new resourceAffectation to the store.
   *
   * @param resourcePlanningId
   * @param date
   * @param newResourceAffectation
   */
  public setResourceAffectation(
      resourcePlanningId: ResourcePlanning['id'],
      date: Date,
      newResourceAffectation: ResourceAffectation,
      type: DayShiftType,
      dutyAffection: ResourceAffectation): void {
        // Add the new resource affectation object to store :
        if(newResourceAffectation !== undefined) {
          this.resourceAffectationsStore.add(newResourceAffectation);
        }
        if(dutyAffection !== undefined) {
          this.resourceAffectationsStore.add(dutyAffection);
        }
        // Add this resource to the planning resource
        this.resourcePlanningsStore.addResourceAffectations(resourcePlanningId, date, newResourceAffectation, type, dutyAffection);
  }

  /**
   * Remove resource affection object to a resource planning.
   *
   * @param resourcePlanningId
   * @param date
   * @param newResourceAffectation
   */
   public removeResourceAffectation(
    resourcePlanningId: ResourcePlanning['id'],
    date: Date): void {
      // Remove this resource from the resource planning store :
      this.resourcePlanningsStore.removeResourceDayAffectations(resourcePlanningId, date);
    }

}
