import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { Observable, Subscription } from 'rxjs';
import { ResourcePlanning } from './../../../../../../../common/models/planning/resource-planning.model';
import { Component, OnDestroy } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { IAfterGuiAttachedParams, ICellRendererParams } from 'ag-grid-community';
import { PlanningCountersQuery } from 'src/app/common/store/planning-counters/planning-counters.query';
import { TimeCounter } from 'src/app/common/models/planning/time-counter.model';
import { map, tap, switchMap, distinctUntilChanged, filter } from 'rxjs/operators';
import { CellUpdateFlasherHelper } from 'src/app/common/core/grid/helpers/cell-update-flasher.helper';

export type CounterCellParams = {
  data: ResourcePlanning;
} & Omit<ICellRendererParams, 'data'>;

@Component({
  selector: 'epop-planning-counters-cell',
  templateUrl: './planning-counters-cell.component.html',
  styleUrls: ['./planning-counters-cell.component.scss']
})
export class PlanningCountersCellComponent implements ICellRendererAngularComp, OnDestroy {

  public params: CounterCellParams;

  public counter: Observable<TimeCounter>;

  public counterValue: number;

  private cellUpdateHelper: CellUpdateFlasherHelper;

  private subscriptions: Array<Subscription> =[];

  constructor(private planningCountersQuery: PlanningCountersQuery) {}

  agInit(params: CounterCellParams): void {
    this.params = params;
    this.cellUpdateHelper = new CellUpdateFlasherHelper(params);
    const personId = params.data.getResourceId();
    this.counter = this.planningCountersQuery.getCounterOfPerson(personId);
    this.subscriptions.push(
      this.counter.pipe(
        tap(timeCounter => this.counterValue = timeCounter.getInitialCount()),
        switchMap(timerCounter => this.planningCountersQuery.computeResourceShiftCounterValue(timerCounter, this.params.data.getId())),
        filter(value => value !== undefined),
        distinctUntilChanged(),
        tap(_ => this.cellUpdateHelper.onUpdate())
      ).subscribe(v => this.counterValue = v)
    );
  }

  refresh(): boolean {
    // By returning false, we let the grid refresh the cell for us.
    return false;
  }

  renderCounterValue(counterValue: number) {
    return DateHelperService.durationToString(counterValue, 0);
  }

  public ngOnDestroy() {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

}
