import { Component, forwardRef, OnInit, ViewChild } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { MatMenuTrigger } from '@angular/material/menu';

@Component({
  selector: 'epop-color-picker',
  templateUrl: './app-color-picker.component.html',
  styleUrls: ['./app-color-picker.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AppColorPickerComponent),
      multi: true
    }
  ]
})
export class AppColorPickerComponent implements OnInit, ControlValueAccessor {

  @ViewChild(MatMenuTrigger) trigger: MatMenuTrigger;

  private controlValue: string;
  public get value(): string { return this.controlValue; }
  public set value(v: string) {
    if (v !== this.controlValue) {
      this.controlValue = v;
      this.onChange(v);
    }
  }

  public presetColors = [
    '#8a2be2',
    '#d2691f',
    '#fb7f50',
    '#6495ed',
    '#8fbc8f'
  ];

  public isDisabled = false;

  constructor() { }

  public ngOnInit(): void {
  }

  public getDefaultColorValue() {
    return this.value;
  }

  writeValue(value: any): void {
    this.value = value;
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  onColorSelected(color: string) {
    this.value = color;
    this.trigger.closeMenu();
  }

  // Unused controlValueAccessor events :
  onChange = (_) => { };
  onTouched = () => { };

}
