import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';
import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

export type PlanningRequirementsState = EntityState<PlanningRequirement>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'planning-requirements' })
export class PlanningRequirementsStore extends EntityStore<PlanningRequirementsState>{

  constructor() {
    super();
  }

  public updateRequirementEntities(requirement: PlanningRequirement, requiredShiftIds: number[], requiredJobTitles: number[]) {
    this.upsert(requirement.getId(), _oldState => {
      const newState = new PlanningRequirement(requirement);
      newState.requiredShifts = [...requiredShiftIds];
      newState.requiredJobTitles = [...requiredJobTitles];
      return newState;
    }, { baseClass: PlanningRequirement });
  }

  public updateRequirementPlanningValues(requirement: PlanningRequirement, newPlanningValues: Record<string, number>) {
    this.upsert(requirement.getId(), _oldState => {
        const newState = new PlanningRequirement(requirement);
        newState.planning = {...newState.planning};
        newState.setPlanningMinValues(newPlanningValues);
        return newState;
      }, { baseClass: PlanningRequirement }
    );
  }
}
