import { ResourceAffectationsStore } from 'src/app/common/store/resource-affectations/resource-affectations.store';
import { ResourceDayAffectationsQuery } from 'src/app/common/store/resource-day-affectations/resource-day-affectations.query';
import { ObjectHelper } from './../../helpers/ObjectHelper';
import { ResourceDayAffectation, DayShiftType } from './../../models/planning/resource-day-affectation.model';
import { ResourceDayAffectationsStore } from './../resource-day-affectations/resource-day-affectations.store';
import { ResourceAffectation } from 'src/app/common/models/planning/resource-affectation.model';
import { ResourcePlanning } from './../../models/planning/resource-planning.model';
import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

export type ResourcePlanningsState = EntityState<ResourcePlanning>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'resource-plannings' })
export class ResourcePlanningsStore extends EntityStore<ResourcePlanningsState>{
  constructor(
    private resourceDayAffectationsStore: ResourceDayAffectationsStore,
    private resourceAffectationsStore: ResourceAffectationsStore,
    private resourceDayAffectationsQuery: ResourceDayAffectationsQuery) {
    super();
  }

  /**
   * Add a new affectation to a resource at a specific day.
   */
  public addResourceAffectations(
      resourcePlanningId: number,
      date: Date,
      newResourceAffectation: ResourceAffectation,
      type: DayShiftType,
      dutyAffection: ResourceAffectation): void {
    this.update(resourcePlanningId, state => {
      const newState = new ResourcePlanning(state, true);
      let dayAffectationId = newState.getResourceDayAffectations(date);
      if(dayAffectationId === undefined) {
        // At this date, no affectation yet :
        const newDayAffectation = new ResourceDayAffectation({
          id: ObjectHelper.generateUniqueId()
        });
        // Register the new ResourceDayAffectation in the store :
        this.resourceDayAffectationsStore.add(newDayAffectation);
        // Update the state :
        newState.setResourceDayAffectations(date, newDayAffectation.getId());
        dayAffectationId = newDayAffectation.getId();
      }
      // Update resourceDayAffectations store by setting the affectation :
      this.resourceDayAffectationsStore.setAffectation(dayAffectationId, newResourceAffectation, type, dutyAffection);
      return newState;
    });
  }

  /**
   * Remove an affectation from a resource at a specific day.
   */
  public removeResourceDayAffectations(resourcePlanningId: number, date: any): void {
    this.update(resourcePlanningId, state => {
      const newState = new ResourcePlanning(state, true);
      // Remove resource affectation object from stores :
      const dayAffectationId = newState.getResourceDayAffectations(date);
      if(dayAffectationId) {
        const dayAffectation = this.resourceDayAffectationsQuery.getEntity(dayAffectationId);
        // Remove occupation affectation that depend on this day affectation
        const resourceAffectationId = dayAffectation.getResourceAffectationId();
        if(resourceAffectationId !== undefined) {
          this.resourceAffectationsStore.removeResourceAffectation(resourceAffectationId);
        }
        // Remove onDuty affectation that depend on this day affectation
        const onDutyAffectationId = dayAffectation.getOnDutyAffectationId();
        if(onDutyAffectationId !== undefined) {
          this.resourceAffectationsStore.removeResourceAffectation(onDutyAffectationId);
        }
        // Remove the resource day affection from stores :
        this.resourceDayAffectationsStore.remove(dayAffectationId);
        // Finally remove the affectation from the day affectation :
        newState.removeResourceDayAffectations(date);
      }
      return newState;
    });
  }
}
