import { MatExpansionPanel } from '@angular/material/expansion';
import { Component, Input, OnInit, ViewChild } from '@angular/core';

export type PlanningPanelAction = {
  getIcon: () => string;
  onClick: () => void;
};
@Component({
  selector: 'epop-planning-collapsable-panel',
  templateUrl: './planning-collapsable-panel.component.html',
  styleUrls: ['./planning-collapsable-panel.component.scss']
})
export class PlanningCollapsablePanelComponent implements OnInit {

  @Input()
  public panelTitle: string;

  @Input()
  public panelActions: Array<PlanningPanelAction> = [];

  @Input()
  public panelRightActions: Array<PlanningPanelAction> = [];

  @Input()
  public hideCollapseToggler: boolean;

  @ViewChild('expansionPanel')
  public expansionPanel: MatExpansionPanel;

  constructor() { }

  ngOnInit(): void {
  }

  public expandPanel(): void {
    this.expansionPanel.open();
  }

}
