import { PlanningConstraintCategory } from 'src/app/common/models/planning/planning-constraint-category.model';
import { Injectable } from '@angular/core';
import { QueryEntity } from '@datorama/akita';
import { map } from 'rxjs/operators';
import { PlanningConstraintsQuery } from '../planning-constraints/planning-constraint.query';
import { PlanningConstraintCategoriesState, PlanningConstraintCategoriesStore } from './planning-constraint-categories.store';
import { PlanningConstraint } from '../../models/planning/planning-constraint.model';
import { Observable } from 'rxjs';


@Injectable({ providedIn: 'root' })
export class PlanningConstraintCategoriesQuery extends QueryEntity<PlanningConstraintCategoriesState>{
  constructor(
    protected store: PlanningConstraintCategoriesStore,
    private planningConstraintsQuery: PlanningConstraintsQuery) {
      super(store);
  }

  selectAllWithConstaints(): Observable<Record<PlanningConstraintCategory['id'], Array<PlanningConstraint>>> {
    return this.selectAll().pipe(
      map(categoryList => {
        const constrainstList = this.planningConstraintsQuery.getAll();
        const result: Record<PlanningConstraintCategory['id'], Array<PlanningConstraint>> = {};
        categoryList.forEach(category => {
          result[category.getId()] = constrainstList.filter(c => c.getCategoryId() === category.getId());
        });
        return result;
      })
    );
  }
}
