import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';
import { differenceInCalendarDays } from 'date-fns';
import { identifier, serializable } from 'serializr';
import { DateHelperService } from '../../helpers/DateHelper.service';
import { ObjectHelper } from '../../helpers/ObjectHelper';
import { Shift } from './shift.model';

export class TimeCounter {

    @serializable(identifier())
    public id: number;

    @serializable
    public personId: number;

    @serializable
    public initialCount: number;

    constructor(init: Partial<TimeCounter>) {
        if(init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public toString(countValue: number): string {
        let prefix = '';
        if(countValue.valueOf() < 0) {
            prefix = '-';
        } else if(countValue.valueOf() > 0) {
            prefix = '+';
        }
        return `${prefix} ${DateHelperService.durationToString(countValue, 0, true)}`;
    }

    public getPersonId(): number {
        return this.personId;
    }

    public getInitialCount(): number {
        return this.initialCount;
    }

    /**
     * Compute all hours done with the specified shift list
     *
     * @param affectionShifts
     * @returns
     */
    public computeShiftHoursCount(
        affectionShifts: AbstractShift[],
        workingPercentage: number,
        numberOfDayWeek: number = 7,
        completeWeekHours: number = 40,
        startDate: Date,
        endDate: Date
    ): number {
        const nominaleCompensation = completeWeekHours / numberOfDayWeek * workingPercentage / 100;
        const periodCount = (differenceInCalendarDays(endDate, startDate) + 1) * nominaleCompensation;
        let durationCount = this.initialCount + periodCount;
        affectionShifts.forEach(shift => {
            if(shift instanceof Shift) {
                durationCount -= shift.getNbHoursShiftDuration();
            } else if(shift instanceof EventShift) {
                const eventHoursCompensation = shift.getHoursCompensation();
                if(eventHoursCompensation === undefined) {
                    durationCount -= nominaleCompensation;
                } else {
                    durationCount -= eventHoursCompensation;
                }
            }
        });
        return durationCount;
    }

}
