import { Component, forwardRef, Input, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'epop-search-input-field',
  templateUrl: './search-input-field.component.html',
  styleUrls: ['./search-input-field.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SearchInputFieldComponent),
      multi: true
    }
  ]
})
export class SearchInputFieldComponent implements OnInit, ControlValueAccessor {

  private controlValue: string;
  public get inputSearchText(): string { return this.controlValue; }
  public set inputSearchText(v: string) {
    if (v !== this.controlValue) {
      this.controlValue = v;
      this.onChange(v);
    }
  }

  @Input()
  public suffixSvgIcon: string;

  @Input()
  public placeholder: string;

  public isDisabled = false;

  constructor() { }

  ngOnInit(): void {
  }

  writeValue(value: any): void {
    this.inputSearchText = value;
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  // Unused controlValueAccessor events :
  onChange = (_) => { };
  onTouched = () => { };

}
