import { ShiftService } from './../../../../../../../../common/services/epop-api/shift.service';
import { UnitService } from 'src/app/common/services/epop-api/units.service';
import { Shift } from 'src/app/common/models/planning/shift.model';
import { Subscription } from 'rxjs';
import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TranslocoService } from '@ngneat/transloco';
import { ColDef } from 'ag-grid-community';
import { ShiftsStore } from 'src/app/common/store/shifts/shifts.store';
import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { ShiftColorChangedEvent } from '../shift-grid/shift-grid.component';
import { UnitDetailsStore } from 'src/app/common/store/unitDetails/unit-details.store';
import { UnitDetails } from 'src/app/common/models/unit-details.model';
import { UnitDetailsQuery } from 'src/app/common/store/unitDetails/unit-details.query';

export type ShiftListDialogData = {
  unitId: UnitDetails['id'];
};

type ShiftUpdate = {
  backgroundColor?: string;
  textColor?: string;
};

export const DialogShiftListSizes = {
  typeColumn: 75,
  colorColumn: 45,
  durationColumn: 75,
  actionColumn: 45
};

@Component({
  selector: 'epop-shift-list-dialog',
  templateUrl: './shift-list-dialog.component.html',
  styleUrls: ['./shift-list-dialog.component.scss']
})
export class TimeShiftListDialogComponent implements OnInit, OnDestroy {

  private subscriptions: Array<Subscription> = [];
  private shiftToDelete: Array<Shift['id']> = [];
  private shiftToAdd: Array<Shift> = [];
  private shiftToUpdate: Record<Shift['id'], ShiftUpdate> = {};
  public shiftList: Array<Shift>;

  // Column definition :
  public additionalColumnDefs: Array<ColDef> = [
    {
      headerName: this.translocoService.translate('shift.shiftType.label'),
      field: 'shiftType',
      width: DialogShiftListSizes.typeColumn,
      valueFormatter: valF => this.translocoService.translate('shift.shiftType.' + valF.value),
    },
    {
      headerName: this.translocoService.translate('shift.startTime'),
      field: 'startTime',
      width: DialogShiftListSizes.durationColumn
    },
    {
      headerName: this.translocoService.translate('shift.endTime'),
      field: 'endTime',
      width: DialogShiftListSizes.durationColumn
    },
    {
      headerName: this.translocoService.translate('shift.nbMinutesBreakDuration'),
      field: 'nbMinutesBreakDuration',
      valueFormatter: val => DateHelperService.durationToString(0, val.value),
      width: DialogShiftListSizes.durationColumn
    },
    {
      headerName: this.translocoService.translate('shift.nbHoursShiftDuration'),
      field: 'nbHoursShiftDuration',
      valueFormatter: val => DateHelperService.durationToString(val.value, 0),
      width: DialogShiftListSizes.durationColumn
    }
  ];

  constructor(
    public dialogRef: MatDialogRef<TimeShiftListDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ShiftListDialogData,
    private translocoService: TranslocoService,
    private shiftService: ShiftService,
    private unitService: UnitService,
    private unitDetailsQuery: UnitDetailsQuery
    ) { }

  public ngOnInit(): void {
    // Get list of favorite shifts :
    this.subscriptions.push(
        this.unitDetailsQuery.getFavoriteShiftList(this.data.unitId)
          .subscribe(shiftList => this.shiftList = shiftList)
    );
  }

  public ngOnDestroy(): void {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  public onShiftDeleted(shiftId: Shift['id']) {
    // Delete the shift on save :
    this.shiftToDelete.push(shiftId);
    // Remove the shift from the table rows :
    this.shiftList = this.shiftList.filter(shift => shift.getId() !== shiftId);
    // Remove from add, update lists:
    this.shiftToAdd = this.shiftToAdd.filter(shift => shift.getId() !== shiftId);
    delete this.shiftToUpdate[shiftId];
  }

  public onSaveExit() {
    // Add new shift :
    this.unitService.addNewFavoriteShift(this.data.unitId, this.shiftToAdd);
    Object.keys(this.shiftToUpdate).forEach(shiftKey => {
      const shiftId = Number(shiftKey);
      const update: ShiftUpdate = this.shiftToUpdate[shiftId];
      this.shiftService.updateShift(shiftId, update.backgroundColor, update.textColor);
    });
    this.unitService.removeFavoriteShift(this.data.unitId, this.shiftToDelete);
    this.dialogRef.close();
  }

  public onShiftColorChanged(changeEvent: ShiftColorChangedEvent, changeType: ('text' | 'background')): void {
    const shiftUpdates: ShiftUpdate = this.shiftToUpdate[changeEvent.id] ?? {};
    if(changeType === 'text') {
      shiftUpdates.textColor = changeEvent.color;
    } else {
      shiftUpdates.backgroundColor = changeEvent.color;
    }
    this.shiftToUpdate[changeEvent.id] = shiftUpdates;
  }

  public onShiftAdded(newShift: Shift) {
    this.shiftList = [...this.shiftList, newShift];
    this.shiftToAdd.push(newShift);
  }

}
