import { ResourceAffectationsQuery } from './../../../../../../../../common/store/resource-affectations/resource-affectations.query';
import { ResourceAffectation } from 'src/app/common/models/planning/resource-affectation.model';
import { TranslocoService } from '@ngneat/transloco';
import { Component, EventEmitter, Input, OnChanges, Output, OnDestroy } from '@angular/core';
import { ResourceAffectationsStore } from 'src/app/common/store/resource-affectations/resource-affectations.store';
import { ViewChild } from '@angular/core';
import { MatMenuTrigger } from '@angular/material/menu';
import { ResourceDayAffectation } from 'src/app/common/models/planning/resource-day-affectation.model';
import { Subscription } from 'rxjs';

// Define a delay to prevent menu animation break when updating the store
const ACTION_ANIMATION_DELAY = 300;

interface PlanningCellAction {
    getLabel: () => string;
    onClick?: () => void;
    addDivider?: boolean;
    childItems?: Array<PlanningCellAction>;
}

@Component({
    selector: 'epop-planning-cell-menu',
    templateUrl: './planning-cell-menu.component.html',
    styleUrls: ['./planning-cell-menu.component.scss'],
})
export class PlanningCellMenuComponent implements OnChanges, OnDestroy{

    @Input()
    public cellResourceDayAffection: ResourceDayAffectation;

    @Output()
    public editAffectation = new EventEmitter<ResourceDayAffectation>();

    @Output()
    public deleteAffectation = new EventEmitter();

    @ViewChild('cellMenuTrigger', {read: MatMenuTrigger}) contextMenuTrigger: MatMenuTrigger;

    public parentMenuItems: PlanningCellAction[];

    private updateSubscription: Subscription;

    constructor(
        private translocoService: TranslocoService,
        private resourceAffectationsStore: ResourceAffectationsStore,
        private resourceAffectationsQuery: ResourceAffectationsQuery) {}

    public openMenu() {
        this.contextMenuTrigger.openMenu();
    }

    ngOnChanges(): void {
        this.parentMenuItems = this.getMenuItem();
        this.subcribeResourceAffectationChanges();
    }

    private subcribeResourceAffectationChanges(): void {
        const resourceAffectationId =  this.cellResourceDayAffection?.getResourceAffectationId();
        if(resourceAffectationId !== undefined) {
            if(this.updateSubscription === undefined) {
                this.updateSubscription = this.resourceAffectationsQuery
                            .selectEntity(resourceAffectationId).subscribe(p => this.parentMenuItems = this.getMenuItem());
            }
        }
    }

    private getMenuItem(): PlanningCellAction[] {
        const items: Array<PlanningCellAction> = [];
        const resourceAffectationId =  this.cellResourceDayAffection?.getResourceAffectationId();
        if(resourceAffectationId !== undefined) {
            const resourceAffectation = this.resourceAffectationsQuery.getEntity(resourceAffectationId);
             // General action :
             items.push(
                {
                    getLabel: () => this.translocoService.translate('common.crud.modify') + '...',
                    onClick: ()=> this.editResourceAffectation()
                },
                {
                    getLabel: () => this.translocoService.translate('common.crud.delete') + '...',
                    onClick: ()=> this.deleteCellAffectation()
                },
                {
                    getLabel: () => this.getFreezeActionLabel(resourceAffectation),
                    onClick: () => this.changeCellFrozenStatus(resourceAffectation)
                },
                {
                    getLabel: () => this.getDesireActionLabel(resourceAffectation),
                    onClick: () => this.setCellAsDesire(resourceAffectation),
                    addDivider: true
                },
                {
                    getLabel: () => this.getPriorityActionLabel(resourceAffectation),
                    onClick: () => this.setCellAsPriority(resourceAffectation),
                    addDivider: true
                }
            );
        } else {
            // Action without resource affectation :
            items.push(
                {
                    getLabel: () => this.translocoService.translate('common.crud.add'),
                    onClick: () => this.editResourceAffectation()
                },
            );
        }
        return items;
    }

    getDesireActionLabel(cellResourceAffection: ResourceAffectation): string{
        if(cellResourceAffection?.getIsDesired()) {
            return this.translocoService.translate('planningGenerator.planning.cellActions.undesire');
        } else {
            return this.translocoService.translate('planningGenerator.planning.cellActions.desire');
        }
    }

    getPriorityActionLabel(cellResourceAffection: ResourceAffectation): string{
        if(cellResourceAffection?.getIsPriority()) {
            return this.translocoService.translate('planningGenerator.planning.cellActions.unprioritize');
        } else {
            return this.translocoService.translate('planningGenerator.planning.cellActions.prioritize');
        }
    }

    getFreezeActionLabel(cellResourceAffection: ResourceAffectation): string{
        if(cellResourceAffection?.getIsFrozen()) {
            return this.translocoService.translate('planningGenerator.planning.cellActions.unfreeze');
        } else {
            return this.translocoService.translate('planningGenerator.planning.cellActions.freeze');
        }
    }

    changeCellFrozenStatus(cellResourceAffection: ResourceAffectation): void {
        // Use timemout to prevent angular menu from being animated during store update :
        setTimeout(() => {
            this.resourceAffectationsStore
                .setResourceAffectationFreeze(cellResourceAffection.getId(), !cellResourceAffection.getIsFrozen());
        }, ACTION_ANIMATION_DELAY);
    }


    setCellAsDesire(cellResourceAffection: ResourceAffectation): void {
        // Use timemout to prevent angular menu from being animated during store update (can break menu apparence)
        setTimeout(() => {
            this.resourceAffectationsStore
                .setResourceAffectationDesire(cellResourceAffection.getId(), !cellResourceAffection.getIsDesired());
        }, ACTION_ANIMATION_DELAY);
    }

    setCellAsPriority(cellResourceAffection: ResourceAffectation): void {
        // Use timemout to prevent angular menu from being animated during store update (can break menu apparence)
        setTimeout(() => {
            this.resourceAffectationsStore
                .setResourceAffectationPriority(cellResourceAffection.getId(), !cellResourceAffection.getIsPriority());
        }, ACTION_ANIMATION_DELAY);
    }

    editResourceAffectation(): void {
        // Show popop for edition/creation
        this.editAffectation.emit(this.cellResourceDayAffection);
    }

    deleteCellAffectation(): void {
        this.deleteAffectation.emit();
    }

    ngOnDestroy(): void {
        this.updateSubscription?.unsubscribe();
    }

}

