import { ResourceAffectationsQuery } from './../../../../../../common/store/resource-affectations/resource-affectations.query';
import { filter, map } from 'rxjs/operators';
import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';
import { EventShiftsQuery } from 'src/app/common/store/event-shifts/event-shifts.query';
import { UnitDetailsQuery } from 'src/app/common/store/unitDetails/unit-details.query';
import { Subscription } from 'rxjs';
import { ShiftsQuery } from 'src/app/common/store/shifts/shifts.query';
import { ResourcePlanning } from './../../../../../../common/models/planning/resource-planning.model';
import { TranslocoService } from '@ngneat/transloco';
import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ResourceAffectation } from 'src/app/common/models/planning/resource-affectation.model';
import { ResourceDayAffectation, DayShiftType } from 'src/app/common/models/planning/resource-day-affectation.model';
import { Shift } from 'src/app/common/models/planning/shift.model';
import { EventShift } from 'src/app/common/models/planning/event-shift.model';

export interface NewAffectionDialogData {
  resourceAffection: ResourceAffectation;
  resourceDayAffection: ResourceDayAffectation;
  unitId: number;
  date: Date;
}

export interface NewAffectationResultData {
  selectedOccupationShift?: AbstractShift;
  currentOccupationShiftType: DayShiftType;
  selectedOnDuty: Shift;
}

interface ShiftTypeLabel {
  typeLabel: string;
  id: DayShiftType;
}

interface OccupationData {
  workAffectation: AbstractShift | typeof FORM_EMPTY_VALUE;
  event: AbstractShift | typeof FORM_EMPTY_VALUE;
};

const FORM_EMPTY_VALUE = 'none';

@Component({
  selector: 'epop-new-affectation-dialog',
  templateUrl: './new-affectation-dialog.component.html',
  styleUrls: ['./new-affectation-dialog.component.scss']
})
export class NewAffectationDialogComponent implements OnInit, OnDestroy {

  EMPTY_VALUE = FORM_EMPTY_VALUE;

  public selectedData: OccupationData = {
    workAffectation: FORM_EMPTY_VALUE,
    event: FORM_EMPTY_VALUE
  };
  public shiftList = {
    workAffectation: [] as Array<Shift>,
    event: [] as Array<EventShift>,
  };

  public selectedOnDuty: Shift | typeof FORM_EMPTY_VALUE = FORM_EMPTY_VALUE;
  public onDutyList: Array<Shift> = [];

  private subscriptions: Array<Subscription> = [];


  public shiftTypes: Array<ShiftTypeLabel> = [{
      typeLabel: this.translocoService.translate('planningGenerator.planning.affectations.model.workHours'),
      id: 'workAffectation'
    },
    {
      typeLabel: this.translocoService.translate('planningGenerator.planning.affectations.model.eventHours'),
      id: 'event'
    }
  ];

  public currentShiftType: ShiftTypeLabel['id'];

  constructor(
    public dialogRef: MatDialogRef<NewAffectationDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: NewAffectionDialogData,
    private translocoService: TranslocoService,
    private unitDetailsQuery: UnitDetailsQuery,
    private eventShiftsQuery: EventShiftsQuery,
    private resourceAffectationsQuery: ResourceAffectationsQuery,
    private shiftsQuery: ShiftsQuery,
    ) { }

  ngOnInit(): void {
    // Get list for dropdowns :
    this.subscribeToDropdownValues();
    // Pre-fill the form :
    this.currentShiftType = this.data.resourceDayAffection?.getDayType() ?? 'workAffectation';
    // Get default Shift depending of the current type :
    const defaultShift = this.getSelectedShift() ?? FORM_EMPTY_VALUE;
    this.selectedData = {
      event: this.currentShiftType === 'event' ? defaultShift : FORM_EMPTY_VALUE,
      workAffectation: this.currentShiftType === 'workAffectation' ? defaultShift : FORM_EMPTY_VALUE,
    };
    // Get default onDuty :
    const affectationId = this.data.resourceDayAffection?.getOnDutyAffectationId();
    if(affectationId !== undefined) {
      const onDutyAffectation = this.resourceAffectationsQuery.getEntity(affectationId);
      this.selectedOnDuty = this.shiftsQuery.getEntity(onDutyAffectation.getShiftId());
    } else {
      this.selectedOnDuty = FORM_EMPTY_VALUE;
    }
  }

  private subscribeToDropdownValues(): void {
    // Events
    this.subscriptions.push(
      this.unitDetailsQuery.getFavoriteEventShiftList(this.data.unitId).subscribe(l => this.shiftList.event = l)
    );
    // Shifts (without onDuty ones)
    this.subscriptions.push(
      this.unitDetailsQuery.getFavoriteShiftList(this.data.unitId)
        .pipe(
          map(list => list.filter(shift => shift.shiftType !== 'onCallDuty')) // Remove onDuty shifts
        )
        .subscribe(l => this.shiftList.workAffectation = l)
    );
    // OnDuty shifts
    this.subscriptions.push(
      this.unitDetailsQuery.getFavoriteShiftList(this.data.unitId)
        .pipe(
          map(list => list.filter(shift => shift.shiftType === 'onCallDuty')) // only onDuty shifts
        )
        .subscribe(l => this.onDutyList = l)
    );
  }

  private getSelectedShift(): AbstractShift {
    if(this.currentShiftType === 'workAffectation') {
      return this.shiftsQuery.getEntity(this.data.resourceAffection?.getShiftId());
    } else {
      return this.eventShiftsQuery.getEntity(this.data.resourceAffection?.getShiftId());
    };
  }

  public getShiftTypeLabel(shiftTypeId: ShiftTypeLabel['id']): ShiftTypeLabel {
    return this.shiftTypes.find(type => type.id === shiftTypeId);
  }

  public getTitle(): string {
    if(this.isEdition()) {
      // This an edition
      return this.translocoService.translate('planningGenerator.planning.affectations.modify.title');
    } else {
      // This a creation :
      return this.translocoService.translate('planningGenerator.planning.affectations.new.title');
    }
  }

  private isEdition(): boolean {
    return (this.data.resourceAffection !== undefined);
  }

  public getCurrentData(): NewAffectationResultData {
    const selectedData = this.selectedData ? this.selectedData[this.currentShiftType] : undefined;
    return {
      selectedOccupationShift: selectedData !== FORM_EMPTY_VALUE ? selectedData : undefined,
      currentOccupationShiftType: this.currentShiftType,
      selectedOnDuty: this.selectedOnDuty !== FORM_EMPTY_VALUE ? this.selectedOnDuty : undefined
    };
  }

  public ngOnDestroy() {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

}
