import { UnitService } from 'src/app/common/services/epop-api/units.service';
import { UnitDetailsStore } from 'src/app/common/store/unitDetails/unit-details.store';
import { PlanningEditionContextStore } from 'src/app/common/store/planning-edition-context/planning-edition-context.store';
import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Subscription } from 'rxjs';
import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { TeamPlanningsQuery } from 'src/app/common/store/team-plannings/team-plannings.query';

// Define a delay to simulate planning generation
const PLANNING_GENERATION_DELAY = 5000;

export type PlanningGeneratorDialogData = {
  unitId: number;
};

@Component({
  selector: 'epop-planning-generator-dialog',
  templateUrl: './planning-generator-dialog.component.html',
  styleUrls: ['./planning-generator-dialog.component.scss'],
})
export class PlanningGeneratorDialogComponent implements OnInit, OnDestroy {

  public checkBox: boolean;
  public startDate: string;
  public endDate: string;
  public loading: boolean;

  private subscriptions: Array<Subscription> = [];

  constructor(
    public dialogRef: MatDialogRef<PlanningGeneratorDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: PlanningGeneratorDialogData,
    private unitService: UnitService,
    private planningEditionContextStore: PlanningEditionContextStore,
    private teamPlanningsQuery: TeamPlanningsQuery,
    ) { }

  ngOnInit(): void {
    this.checkBox = false;
    this.loading = false;
    this.subscriptions.push(this.teamPlanningsQuery.selectActivePlanningPeriodDates().subscribe(period => {
      if (period && period.length > 1) {
        this.startDate = DateHelperService.toInputString(period[0]);
        this.endDate = DateHelperService.toInputString(period[1]);
      }
    }));
  }

  public ngOnDestroy(): void {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  public onSaveExit() {
    this.loading = true;

    setTimeout(() => {
      this.dialogRef.close();
      this.loading = false;
      this.planningEditionContextStore.setPlanningType('generated');
      this.unitService.generatePlanning(this.data.unitId);
    }, PLANNING_GENERATION_DELAY);
  }

}
