import { Injectable } from '@angular/core';
import { Store, StoreConfig } from '@datorama/akita';
import { PlanningEditionContext } from '../../models/planning/planning-edition-context.model';

@Injectable({
    providedIn: 'root'
})
@StoreConfig({ name: 'planningEditionContextStore' })
export class PlanningEditionContextStore extends Store<PlanningEditionContext> {

    constructor() {
        super(PlanningEditionContextStore.createInitialState());
    }

    static createInitialState(): PlanningEditionContext {
        return {
            cellContentType: 'shifts',
            planningType: 'planned',
            zoomMode: 'normal',
            visualizationPeriod: 'normal',
            sortingAttribute: 'byLastName',
            sortingOrder: 'asc'
        } as PlanningEditionContext;
    }

    public setCellContentType(cellType: PlanningEditionContext['cellContentType']) {
        this.update(state => ({
            ...state,
            cellContentType: cellType
        }));
    }

    public setPlanningType(planningType: PlanningEditionContext['planningType']) {
        this.update(state => ({
            ...state,
            planningType
        }));
    }

    public setZoomMode(zoomMode: PlanningEditionContext['zoomMode']) {
        this.update(state => ({
            ...state,
            zoomMode
        }));
    }

    public setSortingMode(
        sortingAttribute: PlanningEditionContext['sortingAttribute'],
        sortingOrder: PlanningEditionContext['sortingOrder']
        ) {
        this.update(state => ({
            ...state,
            sortingOrder,
            sortingAttribute
        }));
    }

    public setVisualizationPeriod(visualizationPeriod: PlanningEditionContext['visualizationPeriod']) {
        this.update(state => ({
            ...state,
            visualizationPeriod
        }));
    }

}
