import { TeamPlanning } from './team-planning.model';
import { JobTitle } from './job-title.model';
import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';
import { ObjectHelper } from './../../helpers/ObjectHelper';
import { identifier, list, map, object, serializable, primitive } from 'serializr';
import { RequirementValue } from './requirement-value.model';
import { DateHelperService } from '../../helpers/DateHelper.service';

export class PlanningRequirement {

    @serializable(identifier())
    public id: number;

    @serializable(list(primitive()))
    public requiredShifts: AbstractShift['id'][] = [];

    @serializable(list(primitive()))
    public requiredJobTitles: JobTitle['id'][] = [];

    @serializable(map(object(RequirementValue)))
    public planning: Record<string, RequirementValue> = {};

    constructor(init?: Partial<PlanningRequirement>) {
        if(init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    static generateEmptyPlanningRequirement(teamPlanning: TeamPlanning) {
        const newRequirement = new PlanningRequirement({
            id: ObjectHelper.generateUniqueId()
        });
        // Fill requirement days :
        teamPlanning.getPlanningDays().forEach(day => {
            const defaultValue = new RequirementValue({
                minValue: 0
            });
            newRequirement.setPlanning(day, defaultValue);
        });
        return newRequirement;
    }

    public getId(): PlanningRequirement['id'] {
        return this.id;
    }

    public static getPlanningKey(date: Date) {
        return DateHelperService.toHashString(date);
    }

    public setPlanning(date: Date, planningValue: RequirementValue): void {
        const dateKey = PlanningRequirement.getPlanningKey(date);
        this.planning[dateKey] = planningValue;
    }

    public setPlanningMinValues(newPlanningValues: Record<string, number>) {
        Object.keys(newPlanningValues).forEach(dateKey => {
                const oldValue = this.planning[dateKey];
                this.planning[dateKey] = new RequirementValue({
                    ...oldValue,
                    minValue: newPlanningValues[dateKey]
                });
            }
        );
    }

    public getRequirement(day: Date): RequirementValue {
        const planningKey = PlanningRequirement.getPlanningKey(day);
        return this.planning[planningKey];
    }

    public getRequiredShiftIds(): AbstractShift['id'][] {
        return this.requiredShifts;
    }

    public getRequiredJobTitleIds(): JobTitle['id'][] {
        return this.requiredJobTitles;
    }

}
