import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { LocalTime } from './../../../../../../../../common/models/planning/local-time.model';
import { ObjectHelper } from './../../../../../../../../common/helpers/ObjectHelper';
import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Shift, ShiftType } from 'src/app/common/models/planning/shift.model';
import { DialogShiftListSizes } from '../time-shift-dialog/shift-list-dialog.component';

@Component({
  selector: 'epop-inline-shift-form',
  templateUrl: './inline-shift-form.component.html',
  styleUrls: ['./inline-shift-form.component.scss']
})
export class InlineShiftFormComponent implements OnInit {

  @Output()
  shiftCreated: EventEmitter<Shift> = new EventEmitter<Shift>();

  public backgroundColor: string;
  public textColor: string;
  public startTime: Date;
  public endTime: Date;
  public nbMinutesBreakDuration: Date;
  public nbHoursShiftDuration: Date;
  public shiftCode: string;
  public shiftTypes: Array<ShiftType> = ['morning', 'day', 'evening', 'night', 'onCallDuty'];
  public shiftType: ShiftType;

  constructor() { }

  ngOnInit(): void {
    this.clearFields();
  }

  private clearFields(): void {
    const defaultTime = new Date();
    defaultTime.setHours(0,0,0,0);
    this.backgroundColor = '';
    this.textColor = '';
    this.shiftCode = '';
    this.startTime = defaultTime;
    this.endTime = new Date(defaultTime);
    this.nbMinutesBreakDuration = new Date(defaultTime);
    this.nbHoursShiftDuration = new Date(defaultTime);
  }

  public getColorColumnWidth(): string {
    return DialogShiftListSizes.colorColumn + 'px';
  }

  public getDurationColumnWidth(): string {
    return DialogShiftListSizes.durationColumn + 'px';
  }

  public getShiftTypeColumnWidth(): string {
    return DialogShiftListSizes.typeColumn + 'px';
  }

  public getActionColumnWidth(): string {
    return DialogShiftListSizes.actionColumn + 'px';
  }

  public onTimesChanged() {
    this.nbHoursShiftDuration = DateHelperService.getHoursIntervalDuration(this.startTime, this.endTime, this.nbMinutesBreakDuration);
  }

  public isFormValid(): boolean {
    const shiftDuration = DateHelperService.convertDateTimeToDuration(this.nbHoursShiftDuration);
    return this.shiftCode?.length > 0
            && this.shiftType !== undefined
            && (shiftDuration.minutes !== 0 || shiftDuration.hours !== 0);
  }

  public onAdd() {
    const startTimeDuration = DateHelperService.convertDateTimeToDuration(this.startTime);
    const endTimeDuration = DateHelperService.convertDateTimeToDuration(this.endTime);
    const newShift = new Shift({
      id: ObjectHelper.generateUniqueId(),
      code: this.shiftCode,
      rgbBackgroundColor: this.backgroundColor,
      rgbTextColor: this.textColor,
      startTime: new LocalTime(startTimeDuration),
      endTime: new LocalTime(endTimeDuration),
      shiftType: this.shiftType,
      nbHoursShiftDuration: DateHelperService.convertLocalTimeToHours(this.nbHoursShiftDuration),
      nbMinutesBreakDuration: DateHelperService.convertLocalTimeToMinutes(this.nbMinutesBreakDuration),
    });
    this.shiftCreated.next(newShift);
    this.clearFields();
  }


}
